IG$.bA_3/*db_generator*/ = $s.extend($s.window, {
	modal: true,
	isWindow: true,
	region:"center",
	layout: {
		type: "fit",
		align: "stretch"
	},
	
	closable: false,
	resizable:false,
	width: 400,
	autoHeight: true,
	
	callback: null,
	
	init_f: function() {
		var me = this;
		me.down("[name=filetype]").setValue("csv");
	},
	
	_confirm: function() {
		var me = this,
			num_cases = me.down("[name=num_cases]"),
			filetype = me.down("[name=filetype]");
			
		me.callback && me.callback.execute({
			num_cases: num_cases.getValue(),
			filetype: filetype.getValue()
		});
		
		me.close();
	},
			
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_DB_GEN");
		// this.datagrid = Ext.create("Ext.grid.Panel", );
		
		// this.mainpanel = Ext.create("Ext.form.Panel", );
				 
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "fit",
					border: 0,
					defaults: {
						bodyPadding: 10
					},
					items: [
						{
							xtype: "panel",
							layout: "anchor",
							items: [
								{
									xtype: "combobox",
									fieldLabel: "File Type",
									queryMode: 'local',
									name: "filetype",
									displayField: 'name',
									valueField: 'value',
									editable: false,
									autoSelect: true,
									store: {
										xtype: 'store',
										fields: [
											"name", "value"
										],
										data: [
											{name: "CSV File", value: "csv"},
											{name: "Elvira DB File", value: "dbc"},
											{name: "Excel File", value: "xls"},
											{name: "Weka DB File", value: "arff"}
										]
									}
								},
								{
									xtype: "numberfield",
									name: "num_cases",
									fieldLabel: "Number of cases",
									minValue: 100,
									maxValue: 100000,
									value: 5000,
									step: 10
								}
							]
						}
					]
				}
			],
			buttons:[
				"->",
				{
					text: IRm$.r1("B_CONFIRM"),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, {
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui) {
					var panel = this;
					panel.init_f();
				}
			}
		});
		
		IG$.bA_3/*db_generator*/.superclass.initComponent.apply(this, arguments);
	}
});