IG$.bA_5/*tornado_spider*/ = $s.extend($s.window, {
	modal: true,
	isWindow: true,
	layout: "fit",
	
	closable: false,
	resizable:true,
	width: 600,
	height: 550,
	
	_r: {},
	
	init_f: function() {
		var me = this,
			result = me.result,
			tnode = result ? result.cost_effectiveness_results : null,
			tnodes;
		
		if (tnode)
		{
			$.each(tnode, function(i, node) {
				me.d1/*drawChart*/(node);
			});
		}
	},
	
	d1/*drawChart*/: function(rnode) {
		var me = this,
			cname = tnode.name,
			cnode;
		
		switch(cname)
		{
		case "tornado_spider":
			cnode = rnode.tornado;
			cnode && me.d2/*drawTornado*/(cnode);

			cnode = rnode.spider_chart;
			
			if (me._r["p_spider"] == 0)
			{
				cnode && me.d3/*drawSpider*/(cnode, "p_spider");
			}
			else
			{
				me._r["p_spider"] = cnode;
			}
			break;
		case "plot":
			cnode = rnode.plot_chart;
			if (cnode)
			{
				if (me._r["p_plot"] == 0)
				{
					cnode && me.d3/*drawSpider*/(cnode, "p_plot");
				}
				else
				{
					me._r["p_plot"] = cnode;
				}
			}
			break;
		}
	},
	
	d2/*drawTornado*/: function(rnode) {
		var me = this,
			p_tornado = me.down("[name=p_tornado]"),
			p_tornado_doc = $(p_tornado.el.dom),
			tchart,
			categories = [],
			mc, chart = {
				chart: {
					type: "bar"
				},
				title: {
					text: "Tornado"
				},
				xAxis: [
					{
						reversed: false
					},
					{
						opposite: true,
						reversed: false,
						linkedTo: 0,
						labels: {
							step: 1
						}
					}
				],
				yAxis: {
					labels: {
						formatter: function() {
							return Math.abs(this.value);
						}
					}
				},
				plotOptions: {
					series: {
						stacking: "normal"
					}
				},
				series: []
			},
			bnode,
			bnodes,
			i, minvalues = [], maxvalues = [],
			s, e,
			rval;
		
		p_tornado_doc.empty();
		
		tchart = $("<div></div>").appendTo(p_tornado_doc);
		tchart.css({
			width: p_tornado_doc.width(),
			height: p_tornado_doc.height()
		});
		
		categories = rnode.category_key;
		
		categories = categories ? categories.split("\t") : [];
		
		chart.xAxis[0].categories = categories;
		chart.xAxis[1].categories = categories;
		
		chart.chart.renderTo = tchart[0];
		
		bnode = rnode.reference_markers.reference;
		rval = bnode.value;
		rval = Number(rval);
		
		bnode = rnode.bars;
		
		for (i=0; i < bnode.length; i++)
		{
			s = bnode[i].starts;
			e = bnode[i].ends;
			
			minvalues.push(Number(s) - rval);
			maxvalues.push(Number(e) - rval);
		}
		
		chart.series.push({
			name: "Starts",
			data: minvalues
		});
		
		chart.series.push({
			name: "Ends",
			data: maxvalues
		});
		
		mc = new Highcharts.Chart(chart);
	},
	
	d3/*drawSpider*/: function(rnode, pname) {
		var me = this,
			p_spider = me.down("[name=" + pname + "]"),
			p_spider_doc = $(p_spider.el.dom),
			tchart,
			categories = [],
			mc, chart = {
				chart: {
					type: "line"
				},
				title: {
					text: "Spider"
				},
				xAxis: {
				},
				yAxis: {
					labels: {
						formatter: function() {
							return Math.abs(this.value);
						}
					}
				},
				legend: {
					layout: "vertical",
					align: "right",
					verticalAlign: "middle",
					borderWidth: 0
				},
				series: []
			},
			bnode,
			bnodes,
			i,
			s, x, y;
		
		p_spider_doc.empty();
		
		tchart = $("<div></div>").appendTo(p_spider_doc);
		tchart.css({
			width: p_spider_doc.width(),
			height: p_spider_doc.height()
		});
		
		bnode = rnode.series;
		
		if (bnode)
		{
			for (i=0; i < bnode.length; i++)
			{
				s = {
					name: bnode[i].name,
					data: []
				};
				
				if (i == 0)
				{
					x = bnode[i].xvalues;
					categories = x.split("\t");
				}
				
				y = bnode[i].yvalues;
				s.data = y.split("\t");
				
				for (j=0; j < s.data.length; j++)
				{
					s.data[j] = Number(s.data[j]);
				}
				
				chart.series.push(s);
			}
		}
		
		chart.xAxis.categories = categories;
		chart.chart.renderTo = tchart[0];
		
		bnode = rnode.reference_markers.reference;
		if (bnode)
		{
			rval = bnode.value;
			rval = Number(rval);
		}
		
		mc = new Highcharts.Chart(chart);
	},
	
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_TORSP_RS");
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					bodyPadding: 10,
					layout: "fit",
					items: [
						{
							xtype: "tabpanel",
							defaults: {
								deferredRender: false
							},
							flex: 1,
							items: [
								{
									xtype: "panel",
									title: "Tornado",
									layout: "fit",
									name: "p_tornado"
								},
								{
									xtype: "panel",
									title: "Spider",
									layout: "fit",
									name: "p_spider",
									listeners: {
										afterrender: function(tobj) {
											if (panel._r["p_spider"])
											{
												panel.d3/*drawSpider*/.call(panel, panel._r["p_spider"], "p_spider");
											}
											panel._r["p_spider"] = 0;
										}
									}
								},
								{
									xtype: "panel",
									title: "Plot",
									layout: "fit",
									name: "p_plot",
									listeners: {
										afterrender: function(tobj) {
											if (panel._r["p_plot"])
											{
												panel.d3/*drawSpider*/.call(panel, panel._r["p_plot"], "p_plot");
											}
											panel._r["p_plot"] = 0;
										}
									}
								}
							]
						}
					]
				}
			],
			buttons:[
				"->",
				{
					text: IRm$.r1("B_CONFIRM"),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, {
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui) {
					var panel = this;
					panel.init_f();
				}
			}
		});
		
		IG$.bA_5/*tornado_spider*/.superclass.initComponent.apply(this, arguments);
	}
});