function node_tree_viewer(container) {
    this.container = container;
}

node_tree_viewer.prototype = {
    loadTreeData: function(root) {
        var me = this,
            container = me.container,
            tul, tli,
            treebg,
            tw, th,
            coff;
            
        container.empty();
        
        me._treebgc = $("<div class='treebg'></div>").appendTo(container);
        me._treebg = Raphael(me._treebgc[0], 0, 0, "100%", "100%"),
        
        me.tul = tul = $("<ul></ul>").appendTo(container);
        
        me.appendNodeTree(root, tul);
        
        tw = tul[0].scrollWidth || tul.width();
        th = tul[0].scrollHeight || tul.height();
        me._treebgc.width(tw).height(th);
        
        coff = container.offset();
        
        me.rootData = root;
        
        me._treebg.setSize(tw, th);
        me.updateLink(root, coff);
    },
    
    appendNodeTree: function(node, ul) {
        var me = this,
            tli = $("<li class='gsub-box'></li>").appendTo(ul),
            i,
            cul,
            tdiv = $("<div class='gbox'></div>").appendTo(tli),
            tdivzone,
            tbtn;
            
        tdivzone = $("<div class='gname'><div class='gtext'>" + node.name + "</div></div>").appendTo(tdiv);
        tbtn = $("<div class='gbtn'></div>").appendTo($(".gtext", tdivzone));
        
        node.__div = tbtn;
        
        if (node.children && node.children.length)
        {
            cul = $("<ul class='gsub'></ul>").appendTo(tdiv);
            
            tbtn.bind("click", function() {
                tbtn.__v = !tbtn.__v;
                if (tbtn.__v)
                {
                    tbtn.addClass("ghide");
                }
                else
                {
                    tbtn.removeClass("ghide");
                }
                
                me.container.scrollTop(0);
                me.container.scrollLeft(0);
                
                cul.toggle();
                
                me._treebg.clear();
                
                setTimeout(function() {
                    me.validateLinks();
                }, 50);
            });
            
            $.each(node.children, function(i, cnode) {
                me.appendNodeTree(cnode, cul);
            });
        }
    },
    
    validateLinks: function() {
        var me = this,
            tul = me.tul,
            tw,
            th,
            coff,
            container = me.container;
            
        tw = tul[0].scrollWidth || tul.width();
        th = tul[0].scrollHeight || tul.height();
        me._treebgc.width(tw).height(th);
        
        coff = container.offset();
        
        me._treebg.clear();
        me._treebg.setSize(tw, th);
        me.updateLink(me.rootData, coff);
    },
    
    updateLink: function(tnode, coff) {
        var me = this,
            i,
            _treebg = me._treebg,
            line,
            st,
            tdiv,
            toff;
            
        if (tnode.__div)
        {
            st = tnode.__div.offset();
            
            if (tnode.children && tnode.children.length)
            {
                for (i=0; i < tnode.children.length; i++)
                {
                    tdiv = tnode.children[i].__div;
                    if (tdiv)
                    {
                        toff = tdiv.offset();
                        line = _treebg.path("M" + (st.left - coff.left + 20) + " " + (st.top - coff.top + 10) + "L" + (toff.left - coff.left - 100) + " " + (toff.top - coff.top + 10));
                        
                        if (tnode.children[i].is_best_decistion == "T")
                        {
                        	line.attr("stroke", "#ff0000");
                        	line.attr("stroke-width", 4);
                        }
                        if (!tdiv.__v)
                        {    
                            me.updateLink(tnode.children[i], coff);
                        }
                    }
                }
            }
        }
    },
    
    setSize: function(w, h) {
        var me = this;
        
        me.container.width(w).height(h);
    }
}