IG$.BC$scD/*hadoop_shellcommand*/ = $s.extend($s.window, {
	
	modal: true,
	isWindow: true,
	region:"center",
	layout: "fit",
	
	closable: false,
	resizable:false,
	width: 500,
	autoHeight: true,
		
	callback: null,
	
	init_f: function() {
		var me = this,
			msgresult = me.down("[name=msgresult]");
			
		msgresult.setValue(me.msg);
	},
	
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_BG_SCR");
		
		$s.apply(this, {
			items: [
				{
					xtype: "form",
					layout: "anchor",
					defaults: {
						anchor: "100%"
					},
					items: [
						{
							xtype: "textarea",
							name: "msgresult",
							height: 200
						}
					]
				}
			],
			buttons:[
				"->",
				{
					text: IRm$.r1("B_CLOSE"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui) {
					var panel = this;
					panel.init_f();
				}
			}
		});
		
		IG$.BC$scD/*hadoop_shellcommand*/.superclass.initComponent.apply(this, arguments);
	}
});

BC$sc = $s.extend(IG$.BC$base/*hadoop_dlg_base*/, {

	region:"center",
	layout: "fit",

	autoHeight: true,
		
	callback: null,
	
	init_f: function() {
		var me = this,
			job = me.job;
		
		me.dsscript = me.down("[name=dsscript]");
		
		BC$sc.superclass.init_f.call(me, arguments);
		
		if (job)
		{
			me.dsscript.setValue(Base64.decode(job.dsscript));
		}
	},
	
	
	_confirm: function() {
		var me = this,
			job = me.job;
		
		if (job)
		{
			job.dsscript = Base64.encode(me.dsscript.getValue());
		}
		
		me.closeDlg();
	},
	
	closeDlg: function() {
		var me = this;
		me.callback && me.callback.execute(me.job);
		me.fireEvent("close_dlg", me);
	},
	
	ts/*testShellCommand*/: function() {
		var me = this,
			panel = this,
			dsscript = me.dsscript.getValue(),
			req = new IG$._rpc$(),
			cnt = {
				sid: me.job.sid,
				command: Base64.encode(dsscript)
			};
		
		req.init(panel, 
			{
				ack: "bizop",
				payload: {sid: me.job.sid, option: "command"},
				mbody: cnt
			}, panel, panel.rs_ts/*testShellCommand*/, null);
		req.send();
	},
	
	rs_ts/*testShellCommand*/: function(xdoc) {
		var me = this,
			errmsg,
			snode,
			msg, i, tmsg,
			haserr = false;
			
		if (xdoc)
		{
			for (i=0; i < xdoc.length; i++)
			{
				snode = xdoc[i];
				haserr = snode.error;
				if (snode.output && snode.output.length)
				{
					msg = snode.output[0].message;
				}
				
				tmsg = (tmsg) ? tmsg + "\n" + msg : msg;
			}
			
			var dlg = new IG$.BC$scD/*hadoop_shellcommand*/({
				instance: me.instance,
				msg: tmsg
			});
			IG$.checkLogin(this, dlg);
			
			if (haserr == true)
			{
				IG$.alertmsg(ig$.appname, "One or more command failed. See log for detail", null, me, null, null, 1, "error");
			}
		}
	},
		
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_BG_SC");
		
		panel.moption = [
			{
				xtype: "fieldset",
				title: "Advanced Option",
				layout: "anchor",
				defaults: {
					anchor: "100%",
					labelPosition: "top"
				},
				items: [
					{
						xtype: "textarea",
						fieldLabel: "Shell Command",
						name: "dsscript",
						height: 150
					},
					{
						xtype: "fieldcontainer",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "fieldcontainer",
								flex: 1
							},
							{
								xtype: "button",
								text: "Test command",
								handler: function() {
									var me = this;
									me.ts/*testShellCommand*/();
								},
								scope: this
							}
						]
					}
				]
			}
		];
		
		BC$sc.superclass.initComponent.apply(this, arguments);
	}
});