IG$.d$MLearn = $s.extend($s.window, {
	modal: true,
	isWindow: true,
	region:"center",
	layout: {
		type: "fit",
		align: "stretch"
	},
	
	closable: false,
	resizable:false,
	width: 500,
	height: 450,
	
	callback: null,
	
	init_f: function() {
		var me = this,
			jobid = me.jobid,
			req = new IG$._rpc$();
			
		me.lP/*loadParams*/();
					
		if (jobid)
		{
			req.init(me, 
				{
					ack: "print",
					payload: {uid: me.uid, jobid: jobid, type: "csv", option: 'instanceload', appuse: 'T'},
					mbody: {}
				}, me, function(jdoc) {
					var mnode = jdoc,
						hnode = mnode ? jdoc.header : null,
						sheader = me.down("[name=sheader]"),
						smodel = sheader.getSelectionModel(),
						i, dp = [], p;
					
					if (mnode)
					{
						me.__pfile = mnode.downloadurl;
					}
					
					if (hnode)
					{
						for (i=0; i < hnode.length; i++)
						{
							p = {
								name: hnode[i],
								selected: true,
								missing_values: "default",
								discretization: "default"
							};
							dp.push(p);
						}
					}
					
					sheader.store.loadData(dp);
				}
			);
			
			req.send();
		}
	},
	
	lP/*loadParams*/: function() {
		var me = this,
			req;
		me._a1/*loadInit*/();
	},
	
	_a1/*loadInit*/: function() {
		var me = this,
			params = IG$.bml.vars,
			algorithm = me.down("[name=algorithm]"),
			tmissing = me.down("[name=tmissing]");
			
		algorithm.store.loadData(params.learn_algorithms);
		algorithm.setValue(params.learn_algorithms[0].value);
		
		tmissing.store.loadData(params.missing_names);
		tmissing.setValue(params.missing_names[0].value);
	},
	
	_confirm: function() {
		var me = this,
			instance = me.instance,
			sheader = me.down("[name=sheader]"),
			sheader_store = sheader.store,
			rec,
			i,
			modelnet_gr = me.down("[name=modelnet_gr]"),
			modelnet = modelnet_gr.getValue().modelnet,
			muid = null,
			btype = "BayesNet",
			mp = instance.mainPanel,
			__learn_opt;
		
		if (!me.__pfile)
		{
			return;
		}
		
		if (modelnet != "new_model")
		{
			
		}
		
		__learn_opt = me.__learn_opt = {
			pfile: me.__pfile,
			algorithm: me.down("[name=algorithm]").getValue(),
			alg_ind_test: me.down("[name=m_pc1]").getValue(),
			alg_metric: me.down("[name=m_pc2]").getValue(),
			alg_sig_level: me.down("[name=m_pc3]").getValue(),
			alg_alpha: me.down("[name=m_pc4]").getValue(),
			learn_mode: me.down("[name=learn_mode_gr]").getValue().learn_mode,
			model_net: modelnet,
			use_model_net: me.down("[name=use_model_net]").getValue(),
			use_start_learn: me.down("[name=use_start_learn]").getValue(),
			allow_link_addition: me.down("[name=_a1]").getValue(),
			allow_link_removal: me.down("[name=_a2]").getValue(),
			allow_link_inversion: me.down("[name=_a3]").getValue(),
			descretize: me.down("[name=_a4]").getValue(),
			treat_missing_val: me.down("[name=tmissing]").getValue(),
			use_same_num_interval: me.down("[name=_a5]").getValue(),
			same_num_interval: me.down("[name=_a6]").getValue(),
			variable_use: me.down("[name=var_use_gr]").getValue().var_use,
			selected_header: []
		};
		
		for (i=0; i < sheader_store.data.items.length; i++)
		{
			rec = sheader_store.data.items[i];
			__learn_opt.selected_header.push({
				selected: rec.get("selected") ? "T" : "F",
				name: rec.get("name"),
				missing_value: rec.get("missing_values"),
				discretization: rec.get("discretization"),
				num_of_interval: rec.get("num_of_interval")
			});
		}
		
		mp && mp.navigateApp.call(mp, muid, btype.toLowerCase(), btype, null, null, true, null, {
			__learn_opt: __learn_opt
		});
		
		me.callback && me.callback.execute(__learn_opt);
		me.close();
	},
				
	initComponent : function() {
		var me = this,
			fmode = me.jobid ? true : false;
		
		me.title = IRm$.r1("T_ML_LEARN");
		// this.datagrid = Ext.create("Ext.grid.Panel", );
		
		// this.mainpanel = Ext.create("Ext.form.Panel", );
				 
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "fit",
					border: 0,
					defaults: {
						bodyPadding: 5
					},
					items: [
						{
							xtype: "tabpanel",
							deferredRender: false,
							items: [
								{
									xtype: "panel",
									title: "General",
									layout: {
										type: "vbox",
										align: "stretch"
									},
									autoScroll: true,
									padding: ig$.padding,
									items: [
										{
											xtype: "fieldset",
											title: "Database",
											layout: "anchor",
											items: [
												{
													xtype: "fieldcontainer",
													fieldLabel: "Database",
													layout: "anchor",
													items: [
														{
															xtype: "form",
															anchor: "100%",
															name: "mform",
															border: 0,
															layout: "hbox",
															items: [
																{
																	xtype: 'fileuploadfield',
																	name: 'photo',
																	hidden: fmode,
																	allowBlank: false,
																	msgTarget: 'side',
																	width: 320,
																	buttonText: 'Select File...'
																},
																{
																	xtype: "textfield",
																	hidden: !fmode,
																	disabled: true,
																	name: "db_file",
																	value: me.fname || ""
																},
																{
																	  xtype: 'button',
																	  text: 'Upload',
																	  hidden: fmode,
																	  handler: function() {
																		  var me = this,
																			  mform = me.down("[name=mform]");
																		  
																		  if (mform.getForm().isValid()) {
																			mform.getForm().submit({
																				url: ig$.servlet + "/upload",
																				waitMsg: 'Uploading your data file',
																				success: function(fp, o) {
																					IG$.alertmsg('Success', 'Processed file on the server', null, null, 0, "success");
																					var node = fp.errorReader.jsonData,
																						uid = node.result.uid;
																						
																					me.__pfile = uid;
																				}
																			})
																		}
																	},
																	scope: this
																}													
															]
														}
													]
												}
											]
										},
										{
											xtype: "fieldset",
											title: "Algorithm",
											layout: "anchor",
											items: [
												{
													xtype: "combobox",
													fieldLabel: "Algorithm",
													name: "algorithm",
													queryMode: 'local',
													displayField: 'name',
													valueField: 'value',
													editable: false,
													autoSelect: true,
													store: {
														xtype: 'store',
														fields: [
															"name", "value"
														]
													},
													listeners: {
														change: function(tobj, nvalue, ovalue, eopt) {
															var me = this,
																m_p1 = me.down("[name=m_p1]"),
																m_pc1 = me.down("[name=m_pc1]"),
																m_pc2 = me.down("[name=m_pc2]"),
																m_pc3 = me.down("[name=m_pc1]"),
																c = nvalue == "PC";
																
															m_p1.setTitle(nvalue + " algorithm: Options");
															m_p1.show();
															
															m_pc1.setVisible(c);
															m_pc2.setVisible(!c);
															m_pc3.setVisible(c);
														},
														scope: this
													}
												},
												{
													xtype: "textarea",
													anchor: "100%",
													height: 60
												},
												{
													xtype: "fieldset",
													name: "m_p1",
													title: "Algorithm: Options",
													layout: "anchor",
													items: [
														{
															xtype: "combobox",
															fieldLabel: "Independence test",
															name: "m_pc1",
															hidden: true,
															queryMode: 'local',
															displayField: 'name',
															valueField: 'value',
															editable: false,
															autoSelect: true,
															store: {
																xtype: 'store',
																fields: [
																	"name", "value"
																],
																data: [
																	{name: "Cross entropy", value: ""}
																]
															}
														},
														{
															xtype: "combobox",
															fieldLabel: "Metric",
															name: "m_pc2",
															hidden: true,
															queryMode: 'local',
															displayField: 'name',
															valueField: 'value',
															editable: false,
															autoSelect: true,
															store: {
																xtype: 'store',
																fields: [
																	"name", "value"
																],
																data: [
																	{name: "K2", value: ""},
																	{name: "Bayesian", value: ""},
																	{name: "BD", value: ""},
																	{name: "MDLM", value: ""},
																	{name: "Entropy", value: ""},
																	{name: "ALC", value: ""}
																]
															}
														},
														{
															xtype: "numberfield",
															name: "m_pc3",
															hidden: true,
															fieldLabel: "Significance level"
														},
														{
															xtype: "numberfield",
															name: "m_pc4",
															fieldLabel: "Alpha parameter"
														},
														{
															xtype: "container",
															layout: {
																type: "hbox",
																align: "stretch"
															},
															items: [
																{
																	xtype: "displayfield",
																	value: "(Laplace-like correction)"
																},
																{
																	xtype: "container",
																	flex: 1
																}
															]
														}
													]
												}
											]
										},
										{
											xtype: "fieldset",
											title: "Learning type",
											items: [
												{
													xtype: "radiogroup",
													columns: 1,
													name: "learn_mode_gr",
													vertical: true,
													items: [
														{
															boxLabel: "Interactive learning",
															name: "learn_mode",
															inputValue: "interactive"
														},
														{
															boxLabel: "Automatic Learning",
															name: "learn_mode",
															inputValue: "automatic",
															checked: true
														}
													]
												}
											]
										}
									]
								},
								{
									xtype: "panel",
									title: "Model network",
									layout: "anchor",
									padding: ig$.padding,
									items: [
										{
											xtype: "fieldset",
											title: "Choose Model Net",
											items: [
												{
													xtype: "radiogroup",
													name: "modelnet_gr",
													columns: 1,
													vertical: true,
													items: [
														{
															boxLabel: "Make new model network",
															name: "modelnet",
															checked: true,
															inputValue: "new_model"
														},
														{
															boxLabel: "Select model network",
															name: "modelnet",
															inputValue: "modelnet"
														}
													]
												}
											]
										},
										{
											xtype: "fieldset",
											title: "Model network use",
											items: [
												{
													xtype: "checkbox",
													name: "use_model_net",
													boxLabel: "Use the information of the nodes"
												},
												{
													xtype: "checkbox",
													name: "use_start_learn",
													boxLabel: "Start learning from model network"
												},
												{
													xtype: "fieldcontainer",
													items: [
														{
															xtype: "checkbox",
															name: "_a1",
															boxLabel: "Allow link addition"
														},
														{
															xtype: "checkbox",
															name: "_a2",
															boxLabel: "Allow link removal"
														},
														{
															xtype: "checkbox",
															name: "_a3",
															boxLabel: "Allow link inversion"
														}
													]
												}
											]
										}
									]
								},
								{
									xtype: "panel",
									title: "Preprocessing",
									layout: {
										type: "vbox",
										align: "stretch"
									},
									padding: ig$.padding,
									items: [
										{
											xtype: "combobox",
											fieldLabel: "Discretize",
											name: "_a4",
											queryMode: 'local',
											displayField: 'name',
											valueField: 'value',
											editable: false,
											autoSelect: true,
											store: {
												xtype: 'store',
												fields: [
													"name", "value"
												],
												data: [
													{name: "Specify for each variable"},
													{name: "Do not discretize"},
													{name: "Equal frequency intervals"},
													{name: "Equal with intervals"}
												]
											}
										},
										{
											xtype: "combobox",
											fieldLabel: "Treat missing values",
											queryMode: 'local',
											name: "tmissing",
											displayField: 'name',
											valueField: 'value',
											editable: false,
											autoSelect: true,
											store: {
												xtype: 'store',
												fields: [
													"name", "value"
												]
											}
										},
										{
											xtype: "checkbox",
											fieldLabel: "Same number of intervals",
											name: "_a5"
										},
										{
											xtype: "numberfield",
											name: "_a6",
											labelWidth: 120,
											//labelAlign: "right",
											// width: 160,
											fieldLabel: "Number of intervals"
										},
										{
											xtype: "radiogroup",
											vertical: true,
											name: "var_use_gr",
											columns: 1,
											items: [
												{
													boxLabel: "Use all variables", name: "var_use", inputValue: "all", checked: true
												},
												{
													boxLabel: "Use only the variables in the model network", name: "var_use", inputValue: "model"
												},
												{
													boxLabel: "Use selected variables", name: "var_use", inputValue: "selection"
												}
											]
										},
										{
											xtype: "gridpanel",
											name: "sheader",
											store: {
												xtype: "store",
												fields: ["name", "selected", "missing_values", "discretization", "num_of_interval"]
											},
											flex: 1,
											plugins: [
												{
													ptype: "cellediting",
													clicksToEdit: 1
												}
											],
											selType: "checkboxmodel",
											selModel: {
												mode: "MULTI",
												checkSelector: "cell"
											},
											columns: [
												// {
												// 	xtype: "checkcolumn",
												// 	dataIndex: "selected",
												// 	width: 30
												// },
												{
													text: "Preprocessing",
													dataIndex: "name",
													flex: 1,
													minWidth: 120
												},
												{
													text: "Missing values",
													dataIndex: "missing_values",
													flex: 1,
													minWidth: 120,
													editor: {
														xtype: "combobox",
														queryMode: 'local',
														displayField: 'name',
														valueField: 'value',
														editable: false,
														autoSelect: true,
														store: {
															xtype: 'store',
															fields: [
																"name", "value"
															],
															data: [
																{name: "Specify for each variable", value: "default"},
																{name: "Keep records with missing values", value: "keep_records"},
																{name: "Erase records with missing values", value: "erase_records"}
															]
														}
													}
												},
												{
													text: "Discretization",
													dataIndex: "discretization",
													flex: 1,
													minWidth: 120,
													editor: {
														xtype: "combobox",
														queryMode: 'local',
														displayField: 'name',
														valueField: 'value',
														editable: false,
														autoSelect: true,
														store: {
															xtype: 'store',
															fields: [
																"name", "value"
															],
															data: [
																{name: "Specify for each variable", value: "default"},
																{name: "Keep records with missing values", value: "keep_records"},
																{name: "Erase records with missing values", value: "erase_records"}
															]
														}
													}
												},
												{
													text: "Number of intervals",
													dataIndex: "num_of_interval",
													width: 120,
													editor: {
														xtype: "numberfield"
													}
												}
											]
										}
									]
								}
							]
						}
					]
				}
			],
			buttons:[
				"->",
				{
					text: IRm$.r1("B_CONFIRM"),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, {
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui) {
					var me = this,
						mform = me.down("[name=mform]");
					
					mform.getForm().errorReader = new IG$.m2ER();
					
					me.init_f();
				}
			}
		});
		
		IG$.d$MLearn.superclass.initComponent.apply(this, arguments);
	}
});