IG$.BC$ds/*hadoop_datasource*/ = $s.extend(IG$.BC$base/*hadoop_dlg_base*/, {
	
	region:"center",
	layout: "fit",
	
	autoHeight: true,
		
	callback: null,
	
	init_f: function() {
		var me = this,
			job = me.job;
		
		me.dsmapper = me.down("[name=dsmapper]");
		me.dstype = me.down("[name=dstype]");
		me.dsfname = me.down("[name=dsfname]");
		me.dstname = me.down("[name=dstname]");
		me.dsskipexist = me.down("[name=dsskipexist]");
		
		IG$.BC$ds/*hadoop_datasource*/.superclass.init_f.call(this, arguments);
			
		if (job)
		{
			me.dsmapper.setValue(job.dsmapper);
			me.dstype.setValue(job.dstype);
			me.dsfname.setValue(job.dsfname);
			me.dstname.setValue(job.dstname);
			me.dsskipexist.setValue(job.dsskipexist == "T" ? true : false);
		}
	},
	
	
	_confirm: function() {
		var me = this,
			job = me.job;
		
		if (job)
		{
			job.dsmapper = me.dsmapper.getValue();
			job.dsfname = me.dsfname.getValue();
			job.dstname = me.dstname.getValue();
			job.dsskipexist = me.dsskipexist.getValue() ? "T" : "F";
			
			job.dstype = me.dstype.getValue();
		}
		
		me.closeDlg();
	},
	
	closeDlg: function() {
		var me = this;
		me.callback && me.callback.execute(me.job);
		me.fireEvent("close_dlg", me);
	},
	
	initComponent : function() {
		var panel = this,
			lw = 120,
			__m/*mode*/ = panel.__m/*mode*/;
			
		panel.title = IRm$.r1("T_BG_DS");
		
		panel.moption = [
			{
				xtype: "fieldset",
				title: "Advanced Option",
				layout: "anchor",
				defaults: {
					anchor: "100%",
					labelPosition: "top"
				},
				items: [
					{
						xtype: "combobox",
						name: "dstype",
						fieldLabel: "Data Type",
						queryMode: "local",
						displayField: "name",
						valueField: "type",
						editable: false,
						autoSelect: true,
						store: {
							xtype: "store",
							fields: [
								"name", "type"
							],
							data: [
								{name: "File", type: "file", selected: 1},
								{name: "Stream type", type: "stream"},
								{name: "Data Query", type: "sql"}
							]
						},
						listeners: {
							change: function(tobj) {
								var me = this,
									tval = tobj.getValue(),
									f_f1 = me.down("[name=f_f1]"),
									f_f2 = me.down("[name=f_f2]");
								
								f_f1.setVisible(tval != "sql");
								f_f2.setVisible(tval == "sql");
							},
							scope: this
						}
					},
					{
						xtype: "fieldcontainer",
						name: "f_f1",
						layout: "anchor",
						defaults: {
							anchor: "100%",
							labelPosition: "top"
						},
						items: [
							{
								xtype: "textarea",
								name: "dsfname",
								minHeight: 80,
								// hidden: !__m/*mode*/,
								fieldLabel: "File/Directory (Server)"
							},
							{
								xtype: "checkbox",
								name: "dsskipexist",
								// hidden: __m/*mode*/,
								fieldLabel: "Skip if exist",
								boxLabel: IRm$.r1("B_ENABLE")
							},
							{
								xtype: "textfield",
								name: "dstname",
								// hidden: __m/*mode*/,
								fieldLabel: "HDFS name"
							}
						]
					},
					{
						xtype: "fieldcontainer",
						name: "f_f2",
						// hidden: true,
						layout: "anchor",
						defaults: {
							anchor: "100%",
							labelPosition: "top"
						},
						items: [
							{
								xtype: "textfield",
								name: "dbpool",
								fieldLabel: "DataSource"
							},
							{
								xtype: "textarea",
								name: "dbsql",
								fieldLabel: "SQL Query"
							}
						]
					},
					{
						xtype: "fieldcontainer",
						// hidden: true,
						fieldLabel: "MapRed",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "textfield",
								flex: 1,
								name: "dsmapper",
								emptyText: IRm$.r1("M_SEL_ITEM")
							},
							{
								xtype: "button",
								iconCls: "icon-toolbar-search",
								tooltip: IRm$.r1("B_SEARCH"),
								handler: function() {
									this.GG1/*getMapClasses*/(this.dsmapper);
								},
								scope: this
							}
						]
					}
				]
			}
		];
		
		IG$.BC$ds/*hadoop_datasource*/.superclass.initComponent.apply(this, arguments);
	}
});