IG$.BC$mr1/*mongo_mapreduce*/ = $s.extend(IG$.BC$base/*hadoop_dlg_base*/, {
	callback: null,
	
	init_f: function() {
		var me = this,
			job = me.job,
			grdparams, i, data=[],
			param, n,
			pval, aval;
		
		me.grdparams = me.down("[name=grdparams]");
		me.rowregex = me.down("[name=rowregex]");
		me.rowsample = me.down("[name=rowsample]");
		me.mrtype = me.down("[name=mrtype]");
		
		IG$.BC$mr1/*mongo_mapreduce*/.superclass.init_f.call(me, arguments);
		
		if (job)
		{
			me.mrtype.setValue(job.mrtype || "regex");
			me.rowregex.setValue(job.rowregex);
			me.rowsample.setValue(job.rowsample);
			
			grdparams = (job.cfields ? job.cfields.split("|") : null);
			
			if (grdparams)
			{
				for (i=0; i < grdparams.length; i++)
				{
					param = {};
					n = grdparams[i].indexOf("=");
					param.name = n > -1 ? grdparams[i].substring(0, n) : "";
					pval = n > -1 ? grdparams[i].substring(n+1) : "";
					
					if (pval.indexOf("^") > -1)
					{
						aval = pval.split("^");
						param.value = aval[0];
						if (aval.length > 1)
						{
							param.isdate = aval[1] == "T";
						}
						if (aval.length > 2)
						{
							param.dformat = aval[2];
						}
					}
					else
					{
						param.value = pval;
					}
					
					if (param.name)
					{
						data.push(param);
					}
				}
			}
			
			me.grdparams.store.loadData(data);
		}
	},
	
	
	_confirm: function() {
		var me = this,
			job = me.job,
			grdparams = me.grdparams, 
			p, cfields = [],
			param, i;
		
		if (job)
		{
			job.mrtype = me.mrtype.getValue();
			job.rowregex = me.rowregex.getValue();
			job.rowsample = me.rowsample.getValue();
			
			for (i=0; i < grdparams.store.data.items.length; i++)
			{
				p = grdparams.store.data.items[i];
				if (p.get("name")) 
				{
					cfields.push(p.get("name") + "=" + p.get("value") + "^" + (p.get("isdate") ? "T" : "F") + "^" + (p.get("dformat") || ""));
				}
			}
			
			job.cfields = cfields.join("|");
		}
		
		me.closeDlg();
	},
	
	closeDlg: function() {
		var me = this;
		me.callback && me.callback.execute(me.job);
		me.fireEvent("close_dlg", me);
	},
	
	_g1/*getSampleData*/: function() {
		var me = this,
			req = new IG$._rpc$(),
			cnt = {
				mrtype: me.mrtype.getValue(),
				rowregex: me.rowregex.getValue(),
				rowsample: me.rowsample.getValue()
			};
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {},
				mbody: cnt
			}, me, function(xdoc) {
				var tnode = IG$.XGetNode(xdoc, "/smsg/results/fields"),
					tnodes = IG$.XGetChildNodes(tnode),
					i, dp = [], p, pval,
					grdparams = me.grdparams;
					
				for (i=0; i < tnodes.length; i++)
				{
					pval = IG$.XGetTextVal(tnodes[i]);
					dp.push({name: pval, value: pval});
				}
					
				grdparams.store.loadData(dp);
			});
		req.send();
	},
	
	_g2/*getDateFormat*/: function(rec) {
		var me = this,
			req = new IG$._rpc$(),
			cnt = {
				option: 'date_format',
				date: rec.get("value")
			};
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {},
				mbody: cnt
			}, me, function(xdoc) {
				var tnode = IG$.XGetNode(xdoc, "/smsg/results"),
					tnodes = IG$.XGetChildNodes(tnode),
					i, dp = [], p, pval;
					
				for (i=0; i < tnodes.length; i++)
				{
					pval = IG$.XGetTextVal(tnodes[i]);
					dp.push(pval);
				}
				
				if (dp.length)
				{
					rec.set("dformat", dp[0]);
				}
			});
		req.send();
	},
		
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_BG_MR");
		
		panel.moption = [
			{
				xtype: "fieldset",
				title: "Advanced Option",
				layout: "anchor",
				defaults: {
					anchor: "100%",
					labelPosition: "top"
				},
				items: [
					{
						xtype: "combobox",
						name: "mrtype",
						fieldLabel: "Data Processor Type",
						queryMode: "local",
						displayField: "name",
						valueField: "value",
						editable: false,
						autoSelect: true,
						store: {
							xtype: "store",
							fields: [
								"name", "value"
							],
							data: [
								{name: "RegEx", value: "regex", selected: 1},
								{name: "Delimiter", value: "delimiter"}
							]
						},
						listeners: {
							change: function(tobj) {
								var me = this,
									tval = tobj.getValue(),
									f_f1 = me.down("[name=f_f1]"),
									f_f2 = me.down("[name=f_f2]");
								
								f_f1.setVisible(tval == "regex");
								f_f2.setVisible(tval != "regex");
							},
							scope: this
						}
					},
					
					{
						xtype: "fieldcontainer",
						layout: "anchor",
						name: "f_f1",
						hidden: true,
						defaults: {
							anchor: "100%",
							labelPosition: "top"
						},
						items: [
							{
								xtype: "textarea",
								name: "rowregex",
								height: 80,
								fieldLabel: "Data Row RegEx",
								emptyText: "Ex: ^(\\\\S+) (\\\\S+) (\\\\S+) \\\\[(.*?)\\\\] \\\"(.*?)\\\" (\\\\S+) (\\\\S+)( \\\"(.*?)\" \\\"(.*?)\\\")?"
							}
					// 		// },
					// 		// {
					// 		// 	xtype: "displayfield",
					// 		// 	value: "Ex: ^(\\\\S+) (\\\\S+) (\\\\S+) \\\\[(.*?)\\\\] \\\"(.*?)\\\" (\\\\S+) (\\\\S+)( \\\"(.*?)\" \\\"(.*?)\\\")?"
					// 		// }
						]
					},
					{
						xtype: "fieldcontainer",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						name: "f_f2",
						hidden: true,
						defaults: {
							labelPosition: "top"
						},
						items: [
							{
								xtype: "textfield",
								name: "m_delim",
								fieldLabel: "Delimiter"
							}
						]
					},
					{
						xtype: "textarea",
						name: "rowsample",
						height: 80,
						fieldLabel: "Sample Text"
					},
					{
						xtype: "fieldcontainer",
						layout: {
							type: "hbox",
							pack: "right"
						},
						items: [
							{
								xtype: "container",
								flex: 1
							},
							{
								xtype: "button",
								text: "Get",
								handler: function() {
									this._g1/*getSampleData*/();
								},
								scope: this
							}
						]
					},
					// {
					// 	xtype: "displayfield",
					// 	value: "Output Fields"
					// },
					{
						xtype: "gridpanel",
						name: "grdparams",
						height: 250,
						store: {
							xtype: "store",
							fields: [
								"name", "value", "isdate", "dformat"
							]
						},
						selType: "checkboxmodel",
						selModel: {
							checkSelector: ".x-grid-cell"
						},
						plugins: [
							{
								ptype: "cellediting",
								clicksToEdit: false
							}
						],
						columns: [
							{
								text: IRm$.r1("B_NAME"),
								minWidth: 80,
								dataIndex: "name",
								editor: {
									allowBlank: false
								}
							},
							{
								text: IRm$.r1("B_VALUE"),
								dataIndex: "value",
								minWidth: 80,
								editor: {
									allowBlank: true
								}
							},
							{
								xtype: "checkcolumn",
								text: "Date",
								dataIndex: "isdate",
								width: 30,
								minWidth: 30,
								listeners: {
									checkchange: function(tobj, rowindex, checked, eopts) {
										var me = this,
											rec = me.grdparams.store.data.items[rowindex];
										if (checked && rec && !rec.get("dformat") && rec.get("value"))
										{
											me._g2/*getDateFormat*/(rec);
										}
									},
									scope: this
								}
							},
							{
								text: "Format",
								minWidth: 70,
								dataIndex: "dformat",
								editor: {
									allowBlank: true,
									listeners: {
										edit: function(editor, context, eopts) {
											
										},
										scope: this
									}
								}
							}
						],
						tbar: [
							{
								xtype: "button",
								text: "Add param",
								handler: function() {
									var grd = this.grdparams;
									grd.store.add({
										name: "",
										value: ""
									});
								},
								scope: this
							},
							{
								xtype: "button",
								text: IRm$.r1("L_REMOVE_SELECTED"),
								handler: function() {
									var grd = this.grdparams,
										selection = grd.getSelectionModel().getSelection();
										
									for (i=selection.length-1; i>=0; i--)
									{
										grd.store.remove(selection[i]);
									}
								},
								scope: this
							}
						]
					}
				]
			}
		];
		
		IG$.BC$mr1/*mongo_mapreduce*/.superclass.initComponent.apply(this, arguments);
	}
});