IG$.BC$op/*hadoop_output*/ = $s.extend(IG$.BC$base/*hadoop_dlg_base*/, {
	region:"center",
	layout: "fit",
	
	autoHeight: true,
		
	callback: null,
	
	init_f: function() {
		var me = this,
			job = me.job;
		
		me.dsfname = me.down("[name=dsfname]");
		me.dscnames = me.down("[name=dscnames]");
		me.dsmname = me.down("[name=dsmname]");
		me.dsruntype = me.down("[name=dsruntype]");
		me.dsdelim = me.down("[name=dsdelim]");
		me.dsdelimval = me.down("[name=dsdelimval]");
		me.dstcube = me.down("[name=dstcube]");
		me.dsmeasuredelim = me.down("[name=dsmeasuredelim]");
		me.dsmeasuredelimval = me.down("[name=dsmeasuredelimval]");
		me.dscsdatause = me.down("[name=dscsdatause]");
		me.dscsdatafile = me.down("[name=dscsdatafile]");
		
		IG$.BC$op/*hadoop_output*/.superclass.init_f.call(me, arguments);
		
		if (job)
		{
			me.dsfname.setValue(job.dsfname);
			me.dscnames.setValue(job.dscnames);
			me.dsmname.setValue(job.dsmname);
			me.dsruntype.setValue(job.dsruntype);
			me.dsdelim.setValue(job.dsdelim);
			me.dsdelimval.setValue(job.dsdelimval);
			me.dstcube.setValue(job.dstcube);
			me.dstcubeuid = job.dstcubeuid;
			me.dsmeasuredelim.setValue(job.dsmeasuredelim);
			me.dsmeasuredelimval.setValue(job.dsmeasuredelimval);
			me.dscsdatause.setValue(job.dscsdatause == "T");
			me.dscsdatafile.setValue(job.dscsdatafile);
			
			me.dscsdatafile.setVisible(job.dscsdatause == "T");
		}
	},
	
	
	_confirm: function() {
		var me = this,
			job = me.job;
		
		if (job)
		{
			job.dsfname = me.dsfname.getValue();
			job.dscnames = me.dscnames.getValue();
			job.dsmname = me.dsmname.getValue();
			job.dsruntype = me.dsruntype.getValue();
			job.dsdelim = me.dsdelim.getValue();
			job.dsdelimval = me.dsdelimval.getValue();
			job.dstcube = me.dstcube.getValue();
			job.dstcubeuid = me.dstcubeuid;
			job.dsmeasuredelim = me.dsmeasuredelim.getValue();
			job.dsmeasuredelimval = me.dsmeasuredelimval.getValue();
			job.dscsdatause = me.dscsdatause.getValue() == true ? "T" : "F";
			job.dscsdatafile = me.dscsdatafile.getValue();
		}
		
		me.closeDlg();
	},
	
	closeDlg: function() {
		var me = this;
		me.callback && me.callback.execute(me.job);
		me.fireEvent("close_dlg", me);
	},
	
	m1/*loadCubeSelection*/: function() {
		var me = this,
			dlgitemsel = new IG$.metaSelectDlg({
				instance: me.instance,
				mode: "newitem"
			});
		dlgitemsel.callback = new IG$.callBackObj(this, this.rs_m1/*loadCubeSelection*/);
		IG$.checkLogin(this, dlgitemsel);
	},
	
	rs_m1/*loadCubeSelection*/: function(item) {
		var panel = this,
			cnt = {},
			req = new IG$._rpc$();
		
		req.init(panel, 
			{
				ack: "writecontent",
				payload: {
					address: item.nodepath + "/" + item.name,
					name: item.name,
					type: "MCube",
					pid: item.uid
				},
				mbody: cnt
			}, panel, panel.rs_m1b/*loadCubeSelection*/, panel.rs_m1a/*loadCubeSelection*/, [item.name, item.nodepath, item.uid, cnt]);
		req.showerror = false;
		req.send();
	},
	
	rs_m1a/*loadCubeSelection*/: function(xdoc, opt) {
		var panel = this,
			itemname = opt[0],
			nodepath = opt[1],
			pitemuid = opt[2],
			cnt = opt[3],
			errcode = IG$.getErrorCode(xdoc);
		
		if (errcode == "0x12e0")
		{
			IG$.confirmMessages(ig$.appname, itemname + " already exist on the server. Would you overwrite existing item with this copy?", function(e) {
				if (e == "yes")
				{
					var req = new IG$._rpc$();
					req.init(panel, 
						{
							ack: "writecontent",
							payload: {
								address: nodepath + "/" + itemname,
								name: itemname,
								type: "MCube",
								pid: pitemuid,
								overwrite: 'T'
							},
							mbody: cnt
						}, panel, panel.rs_m1b/*loadCubeSelection*/, null, [itemname, nodepath]);
					req.send();
				}
			}, panel, panel);
		}
		else
		{
			IG$.ShowErrorMessage(xdoc, panel);
		}
	},
	
	rs_m1b/*loadCubeSelection*/: function(xdoc, prop) {
		var me = this,
			tnode = IG$.XGetNode(xdoc, "/smsg/item"),
			p = (tnode ? IG$.XGetAttrProp(tnode) : null);
			
		if (p)
		{
			me.dstcube.setValue(p.nodepath || p.name);
			me.dstcubeuid = p.uid;
		}
	},
		
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_BG_OP");
		
		panel.moption = [
			{
				xtype: "fieldset",
				title: "Advanced Option",
				layout: "anchor",
				defaults: {
					anchor: "100%",
					labelPosition: "top"
				},
				items: [
					{
						xtype: "textfield",
						fieldLabel: "Data name",
						name: "dsfname"
					},
					{
						xtype: "combobox",
						name: "dsruntype",
						fieldLabel: "Run type",
						queryMode: "local",
						displayField: "name",
						valueField: "value",
						editable: false,
						autoSelect: true,
						store: {
							xtype: "store",
							fields: [
								"name", "value",
							],
							data: [
								// {name: "View in Report", value: "R_REPORT"},
								{name: "Load in database", value: "R_LOAD_DB"},
								{name: "Create M-Cube", value: "R_M_CUBE"}
							]
						},
						listeners: {
							change: function() {
								var me = this,
									dsruntype = me.dsruntype;
									
								me.down("[name=opt_cube]").setVisible(dsruntype.getValue() == "R_M_CUBE");
							},
							scope: this
						}
					},
					{
						xtype: "fieldcontainer",
						fieldLabel: "Custom datafile",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "checkbox",
								boxLabel: IRm$.r1("B_ENABLE"),
								name: "dscsdatause",
								listeners: {
									change: function() {
										var me = this,
											dscsdatafile = me.dscsdatafile,
											dscsdatause = me.dscsdatause;
											
										dscsdatafile.setVisible(dscsdatause.getValue() == true);
									},
									scope: this
								}
							},
							{
								xtype: "textfield",
								flex: 1,
								name: "dscsdatafile"
							}
						]
					},
					{
						xtype: "textarea",
						name: "dscnames",
						fieldLabel: "Extra column names",
						height: 50
					},
					{
						xtype: "textfield",
						name: "dsmname",
						fieldLabel: "Measure column"
					},
					{
						xtype: "fieldcontainer",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "combobox",
								name: "dsdelim",
								fieldLabel: "Field delimiter",
								queryMode: "local",
								valueField: "value",
								displayField: "name",
								editable: false,
								autoSelect: true,
								labelPosition: "top",
								store: {
									xtype: "store",
									fields: [
										"name", "value"
									],
									data: [
										{name: "Comma", value: "comma"},
										
										{name: "Custom value", value: ""},
										
										{name: "Tab", value: "tab"},
										{name: "New line", value: "newline"}
									]
								},
								listeners: {
									change: function() {
										var me = this,
											dsdelim = me.dsdelim,
											dsdelimval = me.dsdelimval;
											
										dsdelimval.setVisible(dsdelim.getValue() == "");
									},
									scope: this
								}
							},
							{
								xtype: "textfield",
								hidden: true,
								flex: 1,
								name: "dsdelimval",
								fieldLabel: ""
							}
						]
					},
					{
						xtype: "fieldcontainer",
						layout: {
							type: "hbox",
							align: "stretch",
							labelPosition: "top"
						},
						items: [
							{
								xtype: "combobox",
								name: "dsmeasuredelim",
								fieldLabel: "Measure delimiter",
								queryMode: "local",
								valueField: "value",
								displayField: "name",
								editable: false,
								autoSelect: true,
								store: {
									xtype: "store",
									fields: [
										"name", "value"
									],
									data: [
										{name: "Comma", value: "comma"},
										
										{name: "Custom value", value: ""},
										
										{name: "Tab", value: "tab"},
										{name: "New line", value: "newline"}
									]
								},
								listeners: {
									change: function() {
										var me = this,
											dsmeasuredelim = me.dsmeasuredelim,
											dsmeasuredelimval = me.dsmeasuredelimval;
											
										dsmeasuredelimval.setVisible(dsmeasuredelim.getValue() == "");
									},
									scope: this
								}
							},
							{
								xtype: "textfield",
								hidden: true,
								flex: 1,
								name: "dsmeasuredelimval",
								fieldLabel: ""
							}
						]
					},
					{
						xtype: "fieldcontainer",
						name: "opt_cube",
						fieldLabel: "Cube",
						labelPosition: "top",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "textfield",
								name: "dstcube",
								flex: 1
							},
							{
								xtype: "button",
								name: "btn_cube",
								text: "Create Cube",
								handler: function() {
									var me = this;
									me.m1/*loadCubeSelection*/();
								},
								scope: this
							}
						]
					}
				]
			}
		];
		
		
		IG$.BC$op/*hadoop_output*/.superclass.initComponent.apply(this, arguments);
	}
});