IG$._IB9c/*ml_bayes_pgmx*/ = $s.extend($s.window, {
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 500,
	height: 500,
	
	callback: null,
	
	init_f: function() {
		var me = this,
			itemobj = me.itemobj;
		
		if (itemobj)
		{
			var instanceid = itemobj.instanceid,
				uid = itemobj.uid,
				req = new IG$._rpc$();
				
			req.init(me, 
				{
					ack: "ml",
					payload: {uid: uid, instanceid: instanceid, action: "restore_pgmx"},
					mbody: itemobj.model,
					meta_type: "bayesmodel"
				}, me, function(jdoc) {
					var txtpgmx = me.down("[name=txtpgmx]"),
						tnode = jdoc,
						tval;
					
					if (tnode)
					{
						tval = JSON.stringify(tnode, null, 2);
						tval = tval.replace(/&/g, "&amp;");
						txtpgmx.setValue(tval);
					}
				}, false);
			req.send();
		}
	},
	
	_l1/*loadURL*/: function(fuid) {
		var me = this,
			m1 = me.down("[name=m1]"),
			m1val = m1.getValue(),
			itemobj = me.itemobj;
		
		m1.clearInvalid();
		
		if (!fuid && !m1val)
		{
			m1.markInvalid("Empty");
			return;
		}
		
		var instanceid = itemobj.instanceid,
			uid = itemobj.uid,
			req = new IG$._rpc$();
			
		req.init(me, 
			{
				ack: "ml",
				payload: {uid: uid, instanceid: instanceid, action: "load_url"},
				mbody: {
					fuid: fuid,
					url: m1val
				}
			}, me, function(jdoc) {
				var txtpgmx = me.down("[name=txtpgmx]"),
					tval;
				
				if (jdoc)
				{
					tval = JSON.stringify(jdoc, null, 2);
					tval = tval.replace(/&/g, "&amp;");
					txtpgmx.setValue(tval);
				}
			}, false);
		req.send();
	},
	
	km1/*uploadFile*/: function() {
		var me = this,
			mform = me.down("[name=mform]"),
			txtfilename = me.down("[name=txtfilename]");
		
		if (mform.getForm().isValid())
		{
			mform.getForm().submit({
				url: ig$.servlet + "/upload",
				waitMsg: 'Uploading your data file',
				success: function(fp, o) {
					IG$.alertmsg('Success', 'Processed file on the server', null, me, 0, "success");
					var node = fp.errorReader.jsonData,
						uid = node ? node.result.uid : null;
					
					uid && me._l1/*loadURL*/(uid);
				}
			})
		}
	},
	
	initComponent: function() {
		var me = this;
		
		me.title = IRm$.r1('L_ML_C7');
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					defaults: {
						anchor: "100%"
					},
					items: [
						{
							xtype: "fieldset",
							title: "Open External Resources",
							region: "north",
							
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "fieldcontainer",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									fieldLabel: "Open from URL",
									items: [
										{
											xtype: "textfield",
											name: "m1",
											flex: 1
										},
										{
											xtype: "button",
											text: "Load",
											handler: function() {
												this._l1/*loadURL*/();
											},
											scope: this
										}
									]
								},
								{
									xtype: "container",
									border: 0,
									layout: "fit",
									items: [
										{
											  xtype: "form",
											  name: "mform",
											  fieldLabel: "File Upload",
											  flex: 1,
											  layout: {
												  type: "hbox",
												  align: "stretch"
											  },
											  items: [
												{
													xtype: "hiddenfield",
													name: "_mts_",
													value: IG$._g$a
												},
												{
													xtype: "fileuploadfield",
													name: "fileupload",
													flex: 1,
													buttonText: IRm$.r1("L_SELECT_FILE"),
													fieldLabel: "File",
													listeners: {
														change: function(tobj, value, eopt) {
															var fname = "",
																dval = "\\",
																txtfilename = me.down("[name=txtfilename]");
															if (value)
															{
																if (value.indexOf("/") > -1)
																{
																	dval = "/";
																}
																value = value.split(dval);
																if (value.length > 0)
																{
																	fname = value[value.length - 1];
																}
															}
															txtfilename.setValue(fname);
														}
													}
												},
												{
													xtype: "hiddenfield",
													name: "txtfilename"
												},
												{
													xtype: "hiddenfield",
													name: "targetfolder",
													value: "upload"
												},
												{
													xtype: "hiddenfield",
													name: "filenamemode",
													value: "blank"
												},
												{
													xtype: "button",
													text: IRm$.r1("L_UPLOAD"),
													handler: function() {
														me.km1/*uploadFile*/();
													},
													scope: this
												}
											]
										}
									]
								}
							]
						},
						{
							xtype: "panel",
							flex: 1,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "textarea",
									flex: 1,
									labelPosition: "top",
									fieldLabel: "PGMX Content",
									region: "center",
									name: "txtpgmx"
								}			
							]
						}
					]
				}
			],
			buttons:[
				{
					text: IRm$.r1('B_CONFIRM'),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						var me = this,
							callback = me.callback,
							txtpgmx = me.down("[name=txtpgmx]");
						
						callback && callback.execute(txtpgmx.getValue());
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$._IB9c/*ml_bayes_pgmx*/.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			var me = this,
				mform = me.down("[name=mform]");
				
			mform.getForm().errorReader = new IG$.m2ER();
			me.init_f();
		}
	}
	
});	