IG$.BC$mr/*hadoop_mapreduce*/ = $s.extend(IG$.BC$base/*hadoop_dlg_base*/, {
	region:"center",
	layout: "fit",
	
	callback: null,
	
	init_f: function() {
		var me = this,
			job = me.job,
			grdparams, i, data=[],
			param, n,
			hinput = false, houtput = false;
		
		me.dsmapper = me.down("[name=dsmapper]");
		me.grdparams = me.down("[name=grdparams]");
		
		IG$.BC$mr/*hadoop_mapreduce*/.superclass.init_f.call(me, arguments);
		
		if (job)
		{
			me.dsmapper.setValue(job.dsmapper);
			grdparams = (job.confparams ? job.confparams.split("|") : null);
			
			if (grdparams)
			{
				for (i=0; i < grdparams.length; i++)
				{
					param = {};
					n = grdparams[i].indexOf("=");
					param.name = n > -1 ? grdparams[i].substring(0, n) : "";
					param.value = n > -1 ? grdparams[i].substring(n+1) : "";
					
					if (param.name)
					{
						if (param.name == "input")
						{
							hinput = true;
						}
						else if (param.name == "output")
						{
							houtput = true;
						}
						data.push(param);
					}
				}
			}
			
			if (!hinput)
			{
				data.push({
					"name": "input",
					"value": ""
				});
			}
			
			if (!houtput)
			{
				data.push({
					"name": "output",
					"value": ""
				});
			}
			
			me.grdparams.store.loadData(data);
		}
	},
	
	
	_confirm: function() {
		var me = this,
			job = me.job,
			grdparams = me.grdparams, 
			p, confparams = [],
			param, i;
		
		if (job)
		{
			job.dsmapper = me.dsmapper.getValue();
			
			for (i=0; i < grdparams.store.data.items.length; i++)
			{
				p = grdparams.store.data.items[i];
				if (p.get("name")) 
				{
					confparams.push(p.get("name") + "=" + p.get("value"));
				}
			}
			
			job.confparams = confparams.join("|");
		}
		
		me.closeDlg();
	},
	
	closeDlg: function() {
		var me = this;
		me.callback && me.callback.execute(me.job);
		me.fireEvent("close_dlg", me);
	},
		
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_BG_MR");
		
		panel.moption = [
			{
				xtype: "fieldset",
				title: "Advanced Option",
				layout: "anchor",
				defaults: {
					anchor: "100%",
					labelPosition: "top"
				},
				items: [
					{
						xtype: "fieldcontainer",
						fieldLabel: "MapRed",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "textfield",
								name: "dsmapper",
								emptyText: IRm$.r1("M_SEL_ITEM"),
								flex: 1
							},
							{
								xtype: "button",
								iconCls: "icon-toolbar-search",
								tooltip: IRm$.r1("B_SEARCH"),
								handler: function() {
									this.GG1/*getMapClasses*/(this.dsmapper);
								},
								scope: this
							}
						]
					},
					{
						xtype: "container",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "displayfield",
								value: "Configuration Parameters"
							},
							{
								xtype: "container",
								flex: 1
							}
						]
					},
					{
						xtype: "gridpanel",
						name: "grdparams",
						height: 120,
						store: {
							xtype: "store",
							fields: [
								"name", "value"
							]
						},
						selType: "checkboxmodel",
						selModel: {
							checkSelector: ".x-grid-cell"
						},
						plugins: [
							{
								ptype: "cellediting",
								clicksToEdit: false
							}
						],
						columns: [
							{
								xtype: "gridcolumn",
								text: IRm$.r1("B_NAME"),
								width: 200,
								dataIndex: "name",
								editor: {
									allowBlank: false
								}
							},
							{
								xtype: "gridcolumn",
								text: IRm$.r1("B_VALUE"),
								dataIndex: "value",
								flex: 1,
								editor: {
									allowBlank: true
								}
							}
						],
						tbar: [
							{
								xtype: "button",
								text: "Add param",
								handler: function() {
									var grd = this.grdparams;
									grd.store.add({
										name: "",
										value: ""
									});
								},
								scope: this
							},
							{
								xtype: "button",
								text: IRm$.r1("L_REMOVE_SELECTED"),
								handler: function() {
									var grd = this.grdparams,
										selection = grd.getSelectionModel().getSelection();
										
									for (i=selection.length-1; i>=0; i--)
									{
										grd.store.remove(selection[i]);
									}
								},
								scope: this
							}
						]
					}
				]
			}
		];
		
		IG$.BC$mr/*hadoop_mapreduce*/.superclass.initComponent.apply(this, arguments);
	}
});




