IG$.BC$mr2/*mongo_document*/ = $s.extend(IG$.BC$base/*hadoop_dlg_base*/, {
	region:"center",
	layout: "fit",
	
	autoHeight: true,
		
	callback: null,
	
	init_f: function() {
		var me = this,
			job = me.job,
			grdparams, i, data=[],
			param, n;
		
		me.grdparams = me.down("[name=grdparams]");
		me.doctmpl = me.down("[name=doctmpl]");
		me.mrnode = me.down("[name=mrnode]");
		me.mrdb = me.down("[name=mrdb]");
		me.mrcol = me.down("[name=mrcol]");
		me.mrdocname = me.down("[name=mrdocname]");
		me.mrsmode = me.down("[name=mrsmode]");
		
		IG$.BC$mr2/*mongo_document*/.superclass.init_f.call(me, arguments);
		
		if (job)
		{
			me.doctmpl.setValue(job.doctmpl);
			me.mrdb.setValue(job.mrdb);
			me.mrcol.setValue(job.mrcol);
			me.mrdocname.setValue(job.mrdocname);
			me.mrsmode.setValue(job.mrsmode || "");
			
			grdparams = (job.confparams ? job.confparams.split("|") : null);
			
			if (grdparams)
			{
				for (i=0; i < grdparams.length; i++)
				{
					param = {};
					n = grdparams[i].indexOf("=");
					param.name = n > -1 ? grdparams[i].substring(0, n) : "";
					param.value = n > -1 ? grdparams[i].substring(n+1) : "";
					
					if (param.name)
					{
						data.push(param);
					}
				}
			}
			
			me.grdparams.store.loadData(data);
			
			me.l1/*loadMongoDB*/();
		}
	},
	
	
	_confirm: function() {
		var me = this,
			job = me.job,
			grdparams = me.grdparams, 
			p, confparams = [],
			param, i;
		
		if (job)
		{
			job.mrnode = me.mrnode.getValue();
			job.doctmpl = me.doctmpl.getValue();
			job.mrdb = me.mrdb.getValue();
			job.mrcol = me.mrcol.getValue();
			job.mrdocname = me.mrdocname.getValue();
			job.mrsmode = me.mrsmode.getValue();
			
			for (i=0; i < grdparams.store.data.items.length; i++)
			{
				p = grdparams.store.data.items[i];
				if (p.get("name")) 
				{
					confparams.push(p.get("name") + "=" + p.get("value"));
				}
			}
			
			job.confparams = confparams.join("|");
		}
		
		me.closeDlg();
	},
	
	closeDlg: function() {
		var me = this;
		me.callback && me.callback.execute(me.job);
		me.fireEvent("close_dlg", me);
	},
	
	l1/*loadMongoDB*/: function() {
		var me = this,
			req = new IG$._rpc$(),
			mrnode = me.mrnode;
		
		req.init(me, 
			{
				ack: "tablecontent",
				payload: {uid: me.uid, dbtype: "mongodb", option: "nosqldb_node"},
				mbody: {}
			}, me, function(xdoc) {
				var tnode = IG$.XGetNode(xdoc, "/smsg"),
					tnodes = IG$.XGetChildNodes(tnode),
					i, dp = [{name: IRm$.r1("B_SELECT"), nodename: ""}], p;
					
				for (i=0; i < tnodes.length; i++)
				{
					p = IG$.XGetAttrProp(tnodes[i]);
					p.nodename = p.nodename || p.name;
					dp.push(p);
				}
				
				mrnode.store.loadData(dp);
				mrnode.setValue(me.job.mrnode || "");
			}, null);
		req.send();
	},
	
	m1/*getProcessing*/: function() {
		var me = this,
			p = me._p/*editor*/,
			mitems,
			cfields,
			mfields = [],
			i, j, n, v, av, mp,
			mval,
			doctmpl = me.down("[name=doctmpl]");
			
		mitems = p.m2/*getParents*/.call(p, me, "mongoreduce");
		
		if (mitems && mitems.length)
		{
			cfields = mitems[0].cfields;
			
			if (cfields)
			{
				cfields = cfields.split("|");
				
				for (i=0; i < cfields.length; i++)
				{
					n = cfields[i];
					j = n.indexOf("=");
					
					if (j > 0)
					{
						v = n.substring(j+1);
						n = n.substring(0, j);
						
						av = n.split("^");
						
						mp = {
							name: n,
							value: av[0]
						};
						
						if (av.length > 1)
						{
							mp.is_date = av[1] == "T";
						}
						
						if (av.length > 2)
						{
							mp.dateformat = av[2];
						}
						
						mfields.push(mp);
					}
				}
				
				mval = "";
				
				for (i=0; i < mfields.length; i++)
				{
					mval = (i == 0) ? mfields[i].name : mval + "\n" + mfields[i].name;
				}
				
				mval && doctmpl.setValue(mval);
			}
		}
	},
		
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_BG_MR");
		
		panel.moption = [
			{
				xtype: "fieldset",
				title: "Advanced Option",
				layout: "anchor",
				defaults: {
					anchor: "100%",
					labelPosition: "top"
				},
				items: [
					{
						xtype: "combobox",
						name: "mrnode",
						fieldLabel: "MongoData",
						queryMode: "local",
						displayField: "name",
						valueField: "nodename",
						editable: false,
						autoSelect: true,
						store: {
							xtype: "store",
							fields: [
								"name", "nodename", "type"
							]
						},
						listeners: {
							change: function(tobj) {
								
							},
							scope: this
						}
					},
					{
						xtype: "textfield",
						fieldLabel: "DB Name",
						name: "mrdb"
					},
					{
						xtype: "textfield",
						fieldLabel: "Collection Name",
						name: "mrcol"
					},
					{
						xtype: "textfield",
						fieldLabel: "Document Name",
						name: "mrdocname"
					},
					{
						xtype: "fieldcontainer",
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "displayfield",
								value: "Document",
								emptyText: IRm$.r1("M_SEL_ITEM"),
								flex: 1
							},
							{
								xtype: "button",
								iconCls: "icon-toolbar-search",
								tooltip: IRm$.r1("B_SEARCH"),
								handler: function() {
									this.m1/*getProcessing*/();
								},
								scope: this
							}
						]
					},
					{
						xtype: "textarea",
						name: "doctmpl",
						height: 200
					},
					{
						xtype: "combobox",
						name: "mrsmode",
						fieldLabel: "Synchronous Write Policy",
						queryMode: "local",
						displayField: "name",
						valueField: "value",
						editable: false,
						autoSelect: true,
						store: {
							xtype: "store",
							fields: [
								"name", "value"
							],
							data: [
								{name: "Disabled", value: ""},
								{name: "Cache-Flush", value: "cf"},
								{name: "Direct-Write", value: "dw"},
								{name: "Direct-Write-With-Cache", value: "dwc"}
							]
						}
					},
					{
						xtype: "gridpanel",
						name: "grdparams",
						height: 120,
						store: {
							xtype: "store",
							fields: [
								"name", "value"
							]
						},
						selType: "checkboxmodel",
						selModel: {
							checkSelector: ".x-grid-cell"
						},
						plugins: [
							{
								ptype: "cellediting",
								clicksToEdit: false
							}
						],
						columns: [
							{
								xtype: "gridcolumn",
								text: IRm$.r1("B_NAME"),
								width: 200,
								dataIndex: "name",
								editor: {
									allowBlank: false
								}
							},
							{
								xtype: "gridcolumn",
								text: IRm$.r1("B_VALUE"),
								dataIndex: "value",
								flex: 1,
								editor: {
									allowBlank: true
								}
							}
						],
						tbar: [
							{
								xtype: "button",
								text: "Add param",
								handler: function() {
									var grd = this.grdparams;
									grd.store.add({
										name: "",
										value: ""
									});
								},
								scope: this
							},
							{
								xtype: "button",
								text: IRm$.r1("L_REMOVE_SELECTED"),
								handler: function() {
									var grd = this.grdparams,
										selection = grd.getSelectionModel().getSelection();
										
									for (i=selection.length-1; i>=0; i--)
									{
										grd.store.remove(selection[i]);
									}
								},
								scope: this
							}
						]
					}
				]
			}
		];
		
		IG$.BC$mr2/*mongo_document*/.superclass.initComponent.apply(this, arguments);
	}
});