IG$.BC$hcp/*hadoop_config_items*/ = {};

IG$.BC$hcp/*hadoop_config_items*/.hconfig = [
	// "HADOOP_HOME", "JAVA_HOME", "CMD_FS_IMPORT", "HADOOP_CONF"
];

IG$.BC$hcp/*hadoop_config_items*/.dconfig = {
	"CURDATE": "DATE;yyyyMMdd",
	"PREVDATE": "DATE;yyyyMMdd;D;-1",
	"PREVMONTH": "DATE;yyyyMMdd;M;-1",
	"PREVYEAR": "DATE;yyyyMMdd;Y;-1",
	"USERNAME": "USERNAME",
	"GROUPNAME": "GROUPNAME",
	"USERID": "USERID",
	"USERNAME": "USERNAME",
	"DEPTNAME": "SQL;IGCBASE;SELECT dname FROM WHERE dname='${GROUPNAME}'"
}

IG$.hadoop_config = $s.extend($s.panel, {
	
	closable: true,
	
	layout: "fit",
	bodyPadding: 10,
	
	iconCls: "icon-ing-docdef",
	
	_uid: "/SYS_Config/systemconfig",
	
	init_f: function() {
		var me = this,
			req,
			grdparams = me.down("[name=grdparams]"),
			i,
			uid = me._uid;
		
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "content",
				payload: {uid: uid},
				mbody: {}
			}, me, me.rs_loadContent, me.rs_F1e/*rs_error*/);
		req.send();
	},
	
	rs_loadContent: function(jdoc) {
		var me = this,
			tnodes,
			i, params = [], param;
		
		if (jdoc && jdoc.data)
		{
			params = jdoc.data;
		}
		
		me.params = params;
		
		me.l1/*loadParam*/();
	},
	
	rs_F1e/*rs_error*/: function(xdoc) {
		var me = this,
			r = true,
			req,
			errcode = IG$.getErrorCode(xdoc),
			uid = me._uid;
		
		if (errcode == "0x1400")
		{
			req = new IG$._rpc$();
			req.init(me, 
				{
					ack: "writecontent",
					payload: {address: uid, name: "systemconfig", type: "PrivateContent"},
					mbody: {data: []}
				}, me, me.rs_loadContent, me.rs_F1w/*writeConfig*/);
			req.send();
			r = false;
		}
		
		return r
	},
	
	rs_F1w/*writeConfig*/: function(xdoc) {
		var me = this;
		
		me.params = [];
				
		me.l1/*loadParam*/();
	},
	
	l1/*loadParam*/: function() {
		var me = this,
			baseparams = IG$.BC$hcp/*hadoop_config_items*/.hconfig,
			dconfig = IG$.BC$hcp/*hadoop_config_items*/.dconfig,
			params = [],
			gparams = me.params,
			gp = {},
			grdparams = me.down("[name=grdparams]"),
			i, key,
			v;

		for (i=0; i < baseparams.length; i++)
		{
			gp[baseparams[i]] = baseparams[i];
		}
		
		for (i=0; i < gparams.length; i++)
		{
			if (gp[gparams[i].name])
			{
				delete gp[gparams[i].name];
			}
			
			if (dconfig[gparams[i].name])
			{
				delete dconfig[gparams[i].name];
			}
			
			v = IG$._decodeVal(gparams[i].value);
			
			params.push({
				name: gparams[i].name,
				type: gparams[i].type,
				value: v,
				dsource: gparams[i].dsource,
				sql_exec_type: gparams[i].sql_exec_type
			});
		}
		
		for (key in gp) {
			params.push({
				name: key,
				type: "SVAR",
				value: ""
			});
		}
		
		for (key in dconfig) {
			params.push({
				type: "DVAR",
				name: key,
				value: dconfig[key]
			});
		}
		
		grdparams.store.loadData(params);
	},
	
	toolbarHandler: function(cmd) {
		var me = this,
			grdparams = me.down("[name=grdparams]");
		
		switch (cmd)
		{
		case "cmd_save":
			me.save_content();
			break;
		case "cmd_refresh":
			me.init_f();
			break;
		case "cmd_add_param":
			
			var	row = grdparams.store.add({
				name: "",
				value: ""
			});
			
			grdparams.getSelectionModel().select(row);
			
			break;
		case "cmd_rm_param":
			var sel = grdparams.getSelectionModel().selected,
				i,
				rec;
				
			for (i=sel.length-1; i>=0; i--)
			{
				grdparams.store.remove(sel.items[i]);
			}
			break;
		}
	},
	
	save_content: function() {
		var me = this,
			grdparams = me.down("[name=grdparams]"),
			i, rec,
			req, cnt = {data: []},
			data = cnt.data,
			sql_exec_type;
		
		for (i=0; i < grdparams.store.data.items.length; i++)
		{
			rec = grdparams.store.data.items[i];
			if (rec.get("name"))
			{
				sql_exec_type = rec.get("sql_exec_type")
				data.push({
					type: (rec.get("type") || "VAR"),
					name: rec.get("name"),
					value: sql_exec_type == "SQL" ? IG$._encodeVal(rec.get("value")) : rec.get("value"),
					sql_exec_type: sql_exec_type,
					dsource: rec.get("dsource")
				});
			}
		}

		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "writecontent",
				payload: {address: me._uid},
				mbody: cnt,
				meta_type: "sysparams"
			}, me, me.rs_save_content, null);
		req.send();
	},
	
	rs_save_content: function(xdoc) {
		var panel = this;
		IG$.alertmsg(ig$.appname, IRm$.r1('M_SAVED'), null, panel, 0, "success");
	},

	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "form",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					border: 0,
					items: [
						{
							xtype: "displayfield",
							value: IRm$.r1("L_V_DESC")
						},
						{
							xtype: "gridpanel",
							name: "grdparams",
							store: {
								xtype: "store",
								fields: [
									"name", "value", "type"
								]
							},
							flex: 1,
							selType: "checkboxmodel",
							selModel: {
								checkSelector: ".x-grid-cell"
							},
							/*
							plugins: [
								{
									ptype: "cellediting",
									clicksToEdit: false
								}
							],
							*/
							columns: [
								{
									xtype: "gridcolumn",
									text: IRm$.r1("B_TYPE"),
									dataIndex: "type",
									width: 80
								},
								{
									xtype: "gridcolumn",
									text: IRm$.r1("B_NAME"),
									dataIndex: "name",
									tdCls: "igc-td-link",
									width: 200
								},
								{
									xtype: "gridcolumn",
									text: IRm$.r1("B_VALUE"),
									flex: 1,
									dataIndex: "value"
								}
							],
							listeners: {
								cellclick: function(view, node, c, rec, rindex, e) {
									if (c == 2)
									{
										var dlg = new IG$.$Dhadoop_cfg({
											instance: me.instance,
											record: rec
										});
										IG$.showDlg(me, dlg);
									}
								},
								scope: this
							}
						}
					]
				}
			],
			tbar: [
				{
					iconCls: "icon-toolbar-save",
					name: "t_save",
					tooltip: IRm$.r1("L_SAVE_CONTENT"),
					handler: function() {
						this.toolbarHandler("cmd_save"); 
					},
					scope: this
				},
				"-",
				{
					iconCls: "icon-refresh",
					tooltip: IRm$.r1("L_REFRESH"),
					handler: function() {
						this.toolbarHandler("cmd_refresh"); 
					},
					scope: this
				},
				"-",
				{
					iconCls: "icon-toolbar-add",
					text: IRm$.r1("L_ADD_VAR"),
					tooltip: IRm$.r1("L_ADD_VAR"),
					handler: function() {
						this.toolbarHandler("cmd_add_param"); 
					},
					scope: this
				},
				{
					iconCls: "icon-grid-delete",
					text: IRm$.r1("L_RM_VAR"),
					tooltip: IRm$.r1("L_RM_VAR"),
					handler: function() {
						this.toolbarHandler("cmd_rm_param"); 
					},
					scope: this
				},
				"-",
				{
					text: "Up",
					tooltip: "Up",
					handler: function() {
					},
					scope: this
				},
				{
					text: "Down",
					tooltip: "Down",
					handler: function() {
					},
					scope: this
				}
			]
		});
			  
		IG$.hadoop_config.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			var me = this;
			me.init_f();
		}
	}
	
});

IG$.$Dhadoop_cfg = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 500,
	autoHeight: true,
	
	callback: null,
	
	init_f: function() {
		var me = this
		
		me.load_dbpool();
	},
	
	load_dbpool: function() {
		var me = this,
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "userdb",
				payload: {address: "/Connections", option: "list"},
				mbody: {}
			}, me, function(jdoc) {
				var me = this,
					d,
					i, tnodes, dp = [],
					dsource = me.down("[name=dsource]");
				
				dp.push({name: "-- Select Database --", uid: ""});
				
				if (jdoc && jdoc.children)
				{
					tnodes = jdoc.children;
					
					for (i=0; i < tnodes.length; i++)
					{
						d = tnodes[i];
						d.name = d.locale_name || d.name;
						dp.push(d);
					}
				}
				
				dsource.store.loadData(dp);
				dsource.setValue("");
				
				if (me.record)
				{
					me.down("[name=mform]").setAllValues(me.record.data);
				}
			}, false);
		req.send();
	},
	
	commit_f: function() {
		var me = this,
			record = me.record,
			error;
		
		if (record)
		{
			error = me.down("[name=mform]").getAllValues(record.data);
			
			if (error)
				return;
			
			record.refresh();
		}
		
		me.callback && me.callback.execute();
		me.close();
	},
	
	initComponent: function() {
		var me = this;
		
		me.title = "System Parameters";
		
		$s.apply(this, {
			
			items: [
				{
					xtype: "form",
					padding: ig$.padding,
					name: "mform",
					layout: "anchor",
					defaults: {
						anchor: "100%"
					},
					items: [
						{
							xtype: "textfield",
							fieldLabel: IRm$.r1("B_NAME"),
							name: "name"
						},
						{
							xtype: "combobox",
							fieldLabel: IRm$.r1("B_TYPE"),
							name: "type",
							displayField: "name",
							valueField: "value",
							store: {
								fields: ["name", "value"],
								data: [
									{name: "System", value: "SYSTEM"},
									{name: "Dynamic value", value: "DVAR"},
									{name: "Static value", value: "SVAR"},
									{name: "SQL Execution", value: "PKG"}
								]
							}
						},
						{
							xtype: "combobox",
							fieldLabel: "Execution Type",
							displayField: "name",
							valueField: "value",
							name: "sql_exec_type",
							store: {
								fields: ["name", "value"],
								data: [
									{name: "Not available", value: ""},
									{name: "SQL", value: "SQL"},
									{name: "JavaProgram", value: "JAVA"}
								]
							}
						},
						{
							xtype: "combobox",
							fieldLabel: "Database",
							name: "dsource",
							displayField: "name",
							valueField: "uid",
							store: {
								fields: ["name", "uid"]
							}
						},
						{
							xtype: "textarea",
							name: "value",
							fieldLabel: "Value",
							height: 300
						}
					]
				}
			],
			buttons:[
				{
					text: IRm$.r1('B_CONFIRM'),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this.commit_f();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.$Dhadoop_cfg.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			var me = this;
			me.init_f();
		}
	}
});

