IG$.dlgML_NetProp = $s.extend($s.window, {
	modal: true,
	isWindow: true,
	region:"center",
	layout: {
		type: "fit",
		align: "stretch"
	},
	
	closable: false,
	resizable:false,
	width: 500,
	height: 450,
	
	callback: null,
	
	init_f: function() {
		var me = this,
			tdata = IG$.bml.vars,
			mtype = me.down("[name=type]"),
			variable_types = me.down("[name=variable_types]"),
			default_states = me.down("[name=default_states]"),
			itemobj = me.itemobj,
			probnet = itemobj.model.probnet;
		
		mtype.store.loadData(tdata.network_types);
		variable_types.store.loadData(tdata.variable_types);
		default_states.store.loadData(tdata.discrete_types);
		
		probnet.type = probnet.type || "BayesianNetwork",
		me.setAllValues(probnet);
	},
	
	_confirm: function() {
		var me = this,
			itemobj = me.itemobj,
			probnet = itemobj.model.probnet;

		if (me.getAllValues(probnet))
			return;
		
		me.callback && me.callback.execute();
		
		me.close();
	},
	
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_NET_PROP");
		// this.datagrid = Ext.create("Ext.grid.Panel", );
		
		// this.mainpanel = Ext.create("Ext.form.Panel", );
				 
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "fit",
					border: 0,
					defaults: {
						bodyPadding: 5
					},
					items: [
						{
							xtype: "tabpanel",
							items: [
								{
									xtype: "panel",
									title: "Definition",
									layout: "anchor",
									items: [
										{
											xtype: "combobox",
											fieldLabel: "Network Type",
											name: "type",
											queryMode: 'local',
											displayField: 'name',
											valueField: 'value',
											editable: false,
											autoSelect: true,
											required: true,
											store: {
												xtype: 'store'
											}
										},
										{
											xtype: "checkbox",
											disabled: true,
											fieldLabel: "Is Object Oriented"
										}
									]
								},
								{
									xtype: "panel",
									title: "Variables",
									layout: "anchor",
									items: [
										{
											xtype: "combobox",
											fieldLabel: "Variable Type",
											name: "variable_types",
											queryMode: 'local',
											required: true,
											displayField: 'name',
											valueField: 'value',
											editable: false,
											autoSelect: true,
											store: {
												xtype: 'store'
											}
										},
										{
											xtype: "combobox",
											fieldLabel: "Default States",
											name: "default_states",
											queryMode: 'local',
											required: true,
											displayField: 'name',
											valueField: 'value',
											editable: false,
											autoSelect: true,
											store: {
												xtype: 'store'
											}
										}
									]
								}
							]
						}
					]
				}
			],
			buttons:[
				"->",
				{
					text: IRm$.r1("B_CONFIRM"),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, {
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui) {
					var panel = this;
					panel.init_f();
				}
			}
		});
		
		IG$.dlgML_NetProp.superclass.initComponent.apply(this, arguments);
	}
});