IG$.BY_1/*ByayesChance*/ = $s.extend(IG$.BC$base/*hadoop_dlg_base*/, {
	
	region:"center",
	layout: "fit",
	
	autoHeight: true,
		
	callback: null,
	
	hide_ierr: 1,
	s_hist: 0,
	
	init_f: function() {
		var me = this,
			job = me.job,
			jobmodel = job.model,
			potential = job.potential,
			additional_properties = {},
			tdata = IG$.bml.vars,
			variable_types = tdata.variable_types,
			purpose_types = tdata.purpose_types,
			potential_types = tdata.potential_types,
			_p/*editor*/ = me._p/*editor*/,
			itemobj = _p/*editor*/.itemobj,
			instanceid = itemobj.instanceid,
			req;
		
		// declare control to variable

		if (jobmodel.additional_properties)
		{
			$.each(jobmodel.additional_properties, function(i, prop) {
				additional_properties[prop.name] = prop.value;
			});
		}
		
		me.down("[name=variable_type]").store.loadData(variable_types || []);
		me.down("[name=purpose]").store.loadData(purpose_types || []);
		me.down("[name=potential_type]").store.loadData(potential_types || []);
		
		IG$.BY_1/*ByayesChance*/.superclass.init_f.call(me, arguments);
		
		if (job)
		{
			var tnode,
				p, c, dnode, dnodes, data, header,
				potential_type,
				p_data,
				i, row, crow, cols, rows,
				j, store, columns, fields, h, column,
				g_p1 = me.down("[name=g_p1]"),
				uncertain_columns, ucols;
			
			me._prob_value_edit = [];
			
			me.down("[name=dsname]").setValue(jobmodel.name);
			
			me.down("[name=relevance]").setValue(additional_properties.Relevance);
			me.down("[name=purpose]").setValue(additional_properties.Purpose);
			
			me.down("[name=dsdesc]").setValue(job.comment);
			me.down("[name=decision]").setValue(jobmodel.decision);
			
			// tnode = jobmodel.domain_values_table;
			
			// if (tnode)
			// {
				// precision, unit, variable_type
			me.down("[name=variable_type]").setValue(jobmodel.type);
			// }
			
			tnode = jobmodel.states;
			data = [];
			if (tnode)
			{
				data = me.g1/*getTableData*/(tnode, "name".split(";"))
			}
			me.down("[name=domain_table]").store.loadData(data);
			
			tnode = jobmodel.node_parents;
			
			data = me.g1/*getTableData*/(tnode, "key;name".split(";"));
			me.down("[name=node_parent]").store.loadData(data);
			
			if (potential)
			{
				potential_type = (potential.type || "TABLE").toUpperCase();
				me.down("[name=potential_type]").setValue(potential_type);
				
				var get_cell = function(c) {
					if (typeof c == 'object')
					{
						return c.name;
					}
					
					return c;
				}

				var potential_values = potential.values ? potential.values.split(" ") : [];
				
				switch(potential_type)
				{
				case "TABLE":
					fields = [];
					p_data = [];
					columns = [];

					cols = 2;

					for (i=0; i < cols; i++)
					{
						column = {
							xtype: "gridcolumn",
							width: 150,
							header: "c" + i,
							dataIndex: "c" + i,
							menuDisabled: true,
							editor: "text"
						};
						columns.push(column);
						fields.push("c" + i);
					}

					p_data = [];

					if (jobmodel.states)
					{
						for (i=0; i < jobmodel.states.length; i++)
						{
							var row = {};
							row.c0 = jobmodel.states[i].name;
							row.c1 = potential_values.length > i ? potential_values[i] : null;
							p_data.push(row);
						}
					}

					g_p1.reconfigure(store, columns);
					g_p1.store.loadData(p_data);

					// dnode = potential.values;

					// if (dnode)
					// {
					// 	cols = dnode.colcnt;
					// 	rows = dnode.rowcnt;
					// 	h = Number(dnode.hc);
						
					// 	uncertain_columns = dnode.uncertain_columns;
					// 	uncertain_columns = uncertain_columns ? uncertain_columns.split(",") : [];
						
					// 	ucols = {};
						
					// 	for (i=0; i < uncertain_columns.length; i++)
					// 	{
					// 		ucols[Number(uncertain_columns[i])] = 1;
					// 	}
						
					// 	dnodes = dnode.rows;
						
					// 	for (i=0; i < cols; i++)
					// 	{
					// 		column = {
					// 			xtype: "gridcolumn",
					// 			width: 150,
					// 			header: "c" + i,
					// 			dataIndex: "c" + i,
					// 			menuDisabled: true,
					// 			editor: "text"
					// 		};
							
					// 		column._lastcolumn = column;
							
					// 		columns.push(column);
					// 		fields.push("c" + i);
							
					// 		dnode.colmap["c" + i] = i;
					// 	}
						
					// 	for (i=0; i < dnodes.length-1; i++)
					// 	{
					// 		row = dnodes[i];
							
					// 		crow = {};
							
					// 		for (j=0; j < row.length; j++)
					// 		{
					// 			crow["c" + j] = get_cell(row[j]);
					// 		}
							
					// 		p_data.push(crow);
					// 	}
						
					// 	if (columns.length)
					// 	{
					// 		store = {
					// 			xtype: "store",
					// 			fields: fields
					// 		};
					// 		g_p1.reconfigure(store, columns);
					// 		g_p1.store.loadData(p_data);
					// 	}
					// }
					break;
				}
			}
		}
	},
	
	g1/*getTableData*/: function(dnode, dnames) {
		var i, j,
			data = [],
			dnodes, r, rd;
		
		if (dnode && dnode.length)
		{
			for (i=0; i < dnode.length; i++)
			{
				r = dnode[i]
				rd = {};
				for (j=0; j < dnames.length; j++)
				{
					rd[dnames[j]] = r[dnames[j]];
				}
				data.push(rd);
			}
		}
		
		return data;
	},
	
	_confirm: function() {
		var me = this,
			job = me.job;

		if (!me.closedlg())
			return;
	},
	
	closedlg: function() {
		var me = this,
			job = me.job,
			jobmodel = job.model,
			additional_properties = {},
			req,
			mbody,
			vtype = me.down("[name=variable_type]"),
			variable_type = vtype.getValue(),
			_editor = me._p/*editor*/,
			r = 1;
		
		$.each(["variable_type", "dsname"], function(i, k) {
			var ctrl = me.down("[name=" + k + "]");
			
			ctrl.clearInvalid();
			
			if (!ctrl.getValue())
			{
				ctrl.markInvalid("Required!");
				r = 0;
			}
		});

		$.each(jobmodel.additional_properties, function(i, prop) {
			additional_properties[prop.name] = prop.value;
		});
		
		if (!r)
		{
			return;
		}
			
		switch (variable_type)
		{
		case "finitStates":
			break;
		case "numeric":
		case "descritized":
			break;
		}

		job.comment = me.down("[name=dsdesc]").getValue();
		jobmodel.name = job.name = me.down("[name=dsname]").getValue();
		
		additional_properties.Relevance = me.down("[name=relevance]").getValue();
		additional_properties.Purpose = me.down("[name=purpose]").getValue();

		$s.apply(jobmodel, {
			decision: me.down("[name=decision]").getValue()
		});
		
		jobmodel.type = variable_type;

		jobmodel.domain_values_table = {
			precision: me.down("[name=precision]").getValue(),
			unit: me.down("[name=unit]").getValue()
		};

		jobmodel.additional_properties = [];

		$.each(additional_properties, function(k, value) {
			jobmodel.additional_properties.push({
				name: k,
				value: value
			});
		});
		
		me._upd_node_prob(jobmodel);
		
		job.dsname = job.name;
		me.callback && me.callback.execute(me.job);
		me.fireEvent("close_dlg", me);

		// req.init(me, 
		// 	{
		// 		ack: "ml",
		// 		payload: {instanceid: me._instanceid, action: "set_property", target: "node"},
		// 		mbody: mbody,
		// 		meta_type: "bayes_node_property"
		// 	}, me, function(xdoc) {
		// 		job.dsname = mbody.cname;
		// 		me.callback && me.callback.execute(me.job);
		// 		me.fireEvent("close_dlg", me);
		// 	}
		// );
		
		// req.send();
	},
	
	_upd_node_prob: function(mbody) {
		var me = this,
			domain_table = me.down("[name=domain_table]"),
			states,
			prob,
			i, rec, d,
			_prob_value_edit = me._prob_value_edit;
		
		states = mbody.states = [];
		
		for (i=0; i < domain_table.store.data.items.length; i++)
		{
			rec = domain_table.store.data.items[i];
			
			if (rec.get("name"))
			{
				states.push({
					name: rec.get("name")
				});
			}
		}
		
		prob = mbody.probability = {};
		prob.potential_type = me.down("[name=potential_type]").getValue();
	},
	
	_v1/*variable_type_changed*/: function(tval) {
		var me = this,
			domain_table = me.down("[name=domain_table]"),
			state_num = me.down("[name=state_num]");
		
		state_num.hide();
		
		switch (tval)
		{
		case "finiteStates":
			break;
		case "discretized":
			break;
		case "numeric":
			state_num.show();
			break;
		}
	},
	
	_std_dom: function(menu) {
		var me = this,
			values = menu.values,
			domain_table = me.down("[name=domain_table]"),
			dp = [],
			i;
		
		for (i=0; i < values.length; i++)
		{
			dp.push({
				name: values[i]
			});
		}
		
		domain_table.store.loadData(dp);
	},
	
	initComponent : function() {
		var panel = this,
			lw = 120,
			__m/*mode*/ = panel.__m/*mode*/,
			bvars = IG$.bml.vars,
			discrete_types = bvars.discrete_types,
			discrete_types_menus = [];
			
		panel._job_name = IRm$.r1("T_BA_1");

		$.each(discrete_types, function(i, m) {
			var menu = {
				text: m.name,
				handler: function() {
					panel._std_dom.call(panel, m)
				}
			};

			discrete_types_menus.push(menu);
		});
		
		panel.moption = [
			{
				xtype: "fieldset",
				title: "Definition",
				layout: "anchor",
				defaults: {
					anchor: "100%"
				},
				items: [
					{
						xtype: "fieldcontainer",
						layout: "hbox",
						items: [
							{
								xtype: "combobox",
								fieldLabel: "Purpose",
								name: "purpose",
								labelWidth: 60,
								width: 180,
								queryMode: 'local',
								displayField: 'name',
								valueField: 'value',
								editable: false,
								autoSelect: true,
								store: {
									xtype: 'store',
									fields: [
										"name", "value"
									]
								}
							},
							{
								xtype: "numberfield",
								labelWidth: 50,
								width: 160,
								name: "relevance",
								fieldLabel: "Relevance"
							}
						]
					},
					{
						xtype: "combobox",
						fieldLabel: "Decision criterion",
						name: "decision",
						queryMode: 'local',
						displayField: 'name',
						valueField: 'value',
						editable: false,
						autoSelect: true,
						store: {
							xtype: 'store',
							fields: [
								"name", "value"
							]
						}
					}
				]
			},
			{
				xtype: "fieldset",
				title: "Domain",
				layout: "anchor",
				items: [
					{
						xtype: "combobox",
						fieldLabel: "Variable type",
						name: "variable_type",
						queryMode: 'local',
						displayField: 'name',
						valueField: 'value',
						editable: false,
						autoSelect: true,
						store: {
							xtype: 'store',
							fields: [
								"name", "value"
							]
						},
						listeners: {
							change: function(tobj) {
								this._v1/*variable_type_changed*/(tobj.getValue());
							},
							scope: this
						}
					},
					{
						xtype: "fieldcontainer",
						fieldLabel: "Precision",
						layout: "hbox",
						name: "state_num",
						hidden: true,
						items: [
							{
								xtype: "numberfield",
								name: "precision",
								width: 80
							},
							{
								xtype: "textfield",
								fieldLabel: "Unit",
								name: "unit",
								labelAlign: "right",
								labelWidth: 80,
								width: 120
							},
							{
								xtype: "container",
								flex: 1
							}
						]
					},
					{
						xtype: "gridpanel",
						hideHeaders: true,
						name: "domain_table",
						height: 150,
						tbar: [
							{
								text: "Standard domains",
								menu: {
									xtype: "menu",
									items: discrete_types_menus
								}
							},
							{
								text: IRm$.r1("B_ADD")
							},
							{
								text: "Delete"
							},
							{
								text: "Up"
							},
							{
								text: "Down"
							}
						],
						store: {
							fields: ["name"]
						},
						columns: [
							{
								text: IRm$.r1("B_NAME"),
								flex: 1,
								dataIndex: "name"
							},
							{
								text: "Lower",
								width: 25,
								dataIndex: "lower"
							},
							{
								text: "Value",
								width: 80,
								dataIndex: "lower_value"
							},
							{
								text: "Value",
								width: 80,
								dataIndex: "higher_value"
							},
							{
								text: "Higher",
								width: 25,
								dataIndex: "higher"
							}
						]
					}
				]
			},
			{
				xtype: "fieldset",
				title: "Parents",
				layout: "anchor",
				items: [
					{
						xtype: "gridpanel",
						hideHeaders: true,
						name: "node_parent",
						height: 200,
						tbar: [
							{
								text: "Add"
							},
							{
								text: "Delete"
							}
						],
						store: {
							fields: ["name"]
						},
						columns: [
							{
								text: IRm$.r1("B_NAME"),
								dataIndex: "name",
								flex: 1
							}
						]
					}
				]
			},
			{
				xtype: "fieldset",
				layout: "anchor",
				title: "Other properties",
				tbar: [
					{
						text: "Add"
					},
					{
						text: "Delete"
					},
					{
						text: "Up"
					},
					{
						text: "Down"
					}
				]
			}
		];
		
		IG$.BY_1/*ByayesChance*/.superclass.initComponent.apply(this, arguments);
	}
});