	toolbarHandler: function(cmd) {
		var me = this,
			itemobj = me.itemobj,
			dlg;
		
		switch (cmd)
		{
		case "cmd_save":
			if (!me.uid)
			{
				me.fV7/*saveAsMetaContent*/(false);
			}
			else
			{
				me.fV6/*saveMetaContent*/(false);
			}
			break;
		case "cmd_saveas":
			me.fV7/*saveAsMetaContent*/(false);
			break;
		case "cmd_load_pgmx":
			dlg = new IG$._IB9c/*ml_bayes_pgmx*/({
				instance: me.instance,
				itemobj: me.itemobj,
				callback: new IG$.callBackObj(me, function(c) {
					var cobj = JSON.parse(c);
					me.m1/*loadPGMX*/(cobj);
				})
			});
			IG$.showDlg(me, dlg);
			break;
		case "cmd_net_prop":
			me.np/*network_properties*/();
			break;
		case "cmd_favorites":
			if (this.uid)
			{
				var req = new IG$._rpc$();
				req.init(me, 
					{
						ack: "helpcontent",
						payload: {uid: me.uid},
						mbody: {option: 'addfavorites'}
					}, me, me.rs_regFavorites, null, null);
				req.send();
			}
			else
			{
				IG$.alertmsg(ig$.appname, IRm$.r1('M_ERR_NSAVE'), null, me, 1, "error");
			}
			break;
		case "cmd_schedule":
			if (me.uid)
			{
				me._prep_model();
				var mreq = [
					{
						ack: "bizop",
						payload: {uid: me.uid, option: "execute"},
						mbody: me.itemobj.model,
						jobkey: "0"
					}
				]; 
				
				IG$.showScheduler(me, me.uid, me.itemtype, mreq, null);
			}
			break;
		case "cmd_net_mode":
			me.setNewWorkingMode();
			break;
		case "cmd_decision_tree":
			me.toggleDecisionTree();
			break;
		case "cmd_learn":
			var dlg = new IG$.d$MLearn({
				instance: me.instance,
				callback: new IG$.callBackObj(me, function(__learn_opt) {
					me.do1/*doLearn*/(__learn_opt);
				})
			});
			IG$.showDlg(me, dlg);
			break;
		case "cmd_db_generator":
			var dlg = new IG$.bA_3/*db_generator*/({
				instance: me.instance,
				callback: new IG$.callBackObj(me, function(param) {
					var mreq = new IG$._rpc$();
					mreq.init(me, 
						{
							ack: "ml",
							payload: {uid: me.uid, instanceid: itemobj.instanceid, action: "db_gen"},
							mbody: param
						}, me, function(jdoc) {
							var fpath, filename;
							
							if (jdoc)
							{
								fpath = jdoc.luid;
								filename = jdoc.filename;
								
								if (fpath && filename)
								{
									$.download(ig$.servlet + "/download", [
										{name: "ack", value: "download"},
										{name: "_mts_", value: IG$._g$a || ""},
										{name: "payload", value: fpath},
										{name: "mbody", value: filename},
										{name: "session_key", value: (window.use_session_key ? ig$._session_key || "empty" : "")}
									], 'POST');
								}
							}
						}
					); 
					mreq.send();
				})
			});
			IG$.showDlg(me, dlg);
			break;
		case "cmd_cost_effct_sen":
		case "cmd_cost_effct_det":
			var atype = (cmd == "cmd_cost_effct_sen" ? 0 : 1), 
				dlg = new IG$.bA_4/*cost_effectiveness*/({
					instance: me.instance,
					atype: atype, 
					uid: me.uid,
					itemobj: itemobj,
					width: atype == 0 ? 600 : 320,
					height: atype == 0 ? 550 : 300,
					callback: new IG$.callBackObj(me, function(param) {
						
					})
				});
			IG$.showDlg(me, dlg);
			break;
		}
	},
	
	do1/*doLearn*/: function(learn_opt, interactive_opt) {
		var me = this,
			itemobj = me.itemobj,
			mreq = new IG$._rpc$(),
			s_learn_opt = {},
			k, oval, i,
			opt;
		
		if (interactive_opt && interactive_opt.interactive_run)
		{
			s_learn_opt.interactive_do = true;
		}
		else
		{
			for (k in learn_opt)
			{
				oval = learn_opt[k];
				
				if (k == "selected_header")
				{
					opt = [];
					
					for (i=0; i < oval.length; i++)
					{
						opt.push({
							selected: oval[i].selected,
							name: oval[i].name,
							missing_value: oval[i].missing_value,
							discretization: oval[i].discretization,
							num_of_interval: oval[i].num_of_interval
						});
					}
					
					s_learn_opt[k] = opt;
				}
				else
				{
					if (oval)
					{
						s_learn_opt[k] = oval;
					}
				}
			}
		}
		
		mreq.init(me, 
			{
				ack: "ml",
				payload: {
					uid: me.uid, 
					instanceid: itemobj.instanceid, 
					action: "learn_network", 
					pfile: (learn_opt ? learn_opt.pfile : null)
				},
				mbody: s_learn_opt,
				meta_type: "learning_options"
			}, me, function(xdoc) {
				me.__learn_opt = null;
				me.rs_M1/*rs_loadContent*/(xdoc, 1);
			}
		); 
		mreq.send();
	},
	
	toggleDecisionTree: function() {
		// INFERENCE_WORKING_MODE, EDITION_WORKING_MODE
		var me = this,
			req = new IG$._rpc$(),
			itemobj = me.itemobj;
		
		if (itemobj && itemobj.instanceid)
		{
			me.lv/*changeviewmode*/(1);
			req.init(me, 
				{
					ack: "ml",
					payload: {uid: me.uid, action: "set_decision_tree", instanceid: itemobj.instanceid},
					mbody: {} 
				}, me, function(jdoc) {
					var me = this;
						
					if (jdoc)
					{
						me.paint_decision_tree(jdoc);
					}
				}, false);
			req.send();
		}
	},
	
	paint_decision_tree: function(tnode) {
		var me = this,
			m_decision_tree = me.down("[name=m_decision_tree]"),
			eldom,
			mdom,
			dnd_viewer = me.dnd_viewer,
			tdata = {};
		
		if (!dnd_viewer)
		{
			eldom = $(m_decision_tree.el.dom);
			mdom = $("<div class='ba-decision-viewer'></div>").appendTo(eldom);
			mdom.width(eldom.width()).height(eldom.height());
			dnd_viewer = new node_tree_viewer(mdom);
			me.dnd_viewer = dnd_viewer;
		}
		
		tdata = tnode ? me.build_decision_data([tnode]) : null;
		
		if (tdata && tdata.length > 0)
		{
			dnd_viewer.loadTreeData(tdata[0]);
		}
	},
	
	build_decision_data: function(tnode) {
		var me = this,
			snode, snodes,
			i, j, p, k,
			ndname,
			tdata = [],
			tobj = {};
			
		for (i=0; i < tnode.length; i++)
		{
			ndname = tnode[i].name;
			
			switch (ndname)
			{
			case "decision_branch":
			case "decision_treenode":
				tobj = {};
				snode = tnode[i].property;
				if (snode)
				{
					for (k in snode)
					{
						p = snode[k];
						tobj[k] = p;
					}
					
					if (ndname == "decision_branch")
					{
						tobj.name = tobj.branch_state || "top";
					}
					else
					{
						tobj.name = tobj.nodename;
					}
				}
				snode = tnode[i].children;
				if (snode)
				{
					tobj.children = me.build_decision_data(snode);
				}
				tdata.push(tobj);
				break;
			}
		}
		
		return tdata;
	},
	
	setNewWorkingMode: function() {
		// INFERENCE_WORKING_MODE, EDITION_WORKING_MODE
		var me = this,
			req = new IG$._rpc$(),
			itemobj = me.itemobj,
			emode = me.emode ? 0 : 1;
			
		me.emode = emode;
		
		if (itemobj && itemobj.instanceid)
		{
			me.lv/*changeviewmode*/(0);
			
			if (me.emode)
			{
				req.init(me, 
					{
						ack: "ml",
						payload: {uid: me.uid, action: "set_working_mode", instanceid: itemobj.instanceid},
						mbody: {} 
					}, me, function(jdoc) {
						var me = this;
							
						if (jdoc)
						{
							me.paint(jdoc, me.emode);
						}
					}, false);
				req.send();
			}
			else
			{
				me.paint(null, me.emode);
			}
		}
	},
	
	paint: function(gnode, emode) {
		var me = this,
			tnode = gnode ? gnode.visualnodes : null,
			p, pn, vn, nd,
			g = {},
			i, j, k, vns, vstat, v1, v2, v3, v4,
			itemobj = me.itemobj;
			
		if (tnode)
		{
			for (i=0; i < tnode.length; i++)
			{
				pn = tnode[i];
				p = {
					name: pn.name,
					visualstates: []
				};
				
				vn = pn.visualstates;
				
				if (vn)
				{
					for (j=0; j < vn.length; j++)
					{
						vstat = {
							name: vn[j].name,
							values: []
						};
						vstat.position = parseInt(vstat.position);
						vstat.ispropagationactive = vstat.ispropagationactive;
						
						v2 = vn[j].values;
						
						if (v2)
						{
							for (k=0; k < v2.length; k++)
							{
								v4 = v2[k];
								
								vstat.values.push({
									barlength: v4.barlength,
									formatvalue: v4.formatvalue
								});
							}
						}
						
						p.visualstates.push(vstat);
					}
				}
				
				g[p.name] = p;
			}
		}
		
		$.each(itemobj.nodes, function(i, nd) {
			nd.paint.call(nd, g[nd.text], emode);
		});
		
		me.bf.plumb.repaintEverything();
	},
	
