	rs_lllE/*runBusinessError*/: function(jdoc) {
		var me = this,
			r = true,
			errcode = jdoc.errcode;
		
		if(errcode == "0x28a0")
		{
			r = false;
			
			var sid = jdoc.errormessage;
				
			if (sid)
			{
				IG$.confirmMessages(ig$.appname, "Already workflow is running on server. Would you like to stop service?", function(e) {
					if (e == "yes")
					{
						var req = new IG$._rpc$();
						req.init(me, 
							{
								ack: "bizop",
								payload: {uid: me.uid, sid: sid, option: "cancelWorkflow"},
								mbody: {}
							}, me, me.rs_ll2/*cancelWorkflow*/, null);
						req.send();
					}
				}, me, me);
			}
		}
		
		return r;
	},
	
