	fV8/*saveNewMetaContent*/: function(item, afterclose) {
		var me = this,
			contentxml,
			req = new IG$._rpc$();

		me._prep_model();
		contentxml = me.itemobj.model;
		
		req.init(me, 
			{
				ack: "writecontent",
				payload: {
					address: item.nodepath + "/" + item.name,
					name: item.name,
					type: (this.itemtype),
					pid: item.uid
				},
				mbody: contentxml,
				meta_type: "bayesmodel"
			}, me, me._rs_procMakeItem, me.rs_procMakeItem, [item.name, afterclose, item.nodepath, item.uid, contentxml]);
		   req.showerror = false;
		req.send();
	},
	
	_rs_procMakeItem: function(jdoc, opt) {
		var me = this,
			itemobj = me.itemobj,
			pnode;
			
		if (jdoc)
		{
			itemobj.item = {
				name: jdoc.name,
				uid: jdoc.uid,
				nodepath: jdoc.nodepath,
				type: jdoc.type
			};
			me.setTitle(itemobj.item.name);
			me.uid = itemobj.item.uid;
		}
	},
	
	rs_procMakeItem: function(xdoc, opt) {
		var me = this,
			itemname = opt[0],
			afterclose = opt[1],
			nodepath = opt[2],
			pitemuid = opt[3],
			contentxml = opt[4],
			errcode = IG$.getErrorCode(xdoc);
		
		if (errcode == "0x12e0")
		{
			IG$.confirmMessages(ig$.appname, itemname + " already exist on the server. Would you overwrite existing item with this copy?", function(e) {
				if (e == "yes")
				{
					var req = new IG$._rpc$();
					req.init(me, 
						{
							ack: "writecontent",
							payload: {
								address: nodepath + "/" + itemname,
								name: itemname,
								type: "BayesNet",
								pid: pitemuid,
								overwrite: 'T'
							},
							mbody: contentxml,
							meta_type: "bayesmodel"
						}, me, me._rs_procMakeItem, null, [itemname, afterclose, nodepath]);
					req.send();
				}
			}, me, me);
		}
		else
		{
			IG$.ShowErrorMessage(xdoc, me);
		}
	},
	
