IG$._I9e/*mr_javaclass*/ = function(xdoc) {
	this.xdoc = xdoc;
	this.P1/*parseContent*/();
}

IG$._I9e/*mr_javaclass*/.prototype = {
	P1/*parseContent*/: function() {
		var me = this,
			xdoc = me.xdoc,
			tnode;
		
		if (me.xdoc)
		{
			tnode = IG$.XGetNode(xdoc, "/smsg/item");
			
			if (tnode)
			{
				me.iteminfo = IG$.XGetAttrProp(tnode);
			}
			
			tnode = IG$.XGetNode(xdoc, "/smsg/item/SourceCode");
			
			if (tnode)
			{
				me.javasource = IG$.XGetTextVal(tnode);
				me.javasource = Base64.decode(me.javasource);
			}
		}
	},
	
	P2/*getDefaultScript*/: function() {
		var me = this,
			r = "",
			pname = me.iteminfo.nodepath.split("/");
		
		pname.splice(0, 2);
		pname.splice(pname.length-1, 1);
		
		r += "package " + pname.join(".") + ";\n\n";
		r += "public class " + me.iteminfo.name + "\n";
		r += "{ \n";
		r += "} \n";
		
		return r;
	},
	
	l2/*toXMLString*/: function() {
		var me = this,
			r = {
				uid: me.iteminfo.uid,
				sourcecode: Base64.encode(me.javasource)
			};
		
		return r;
	}
}

IG$.mr_javaeditor = $s.extend($s.panel, {
	
	closable: true,
	
	layout: "border",
	bodyPadding: 0,
	
	init_f: function() {
		var me = this;
		
		me.loadItemContent();
	},
	
	loadItemContent: function() {
		var panel = this,
			req;
			
		req = new IG$._rpc$();
		req.init(panel, 
			{
				ack: "content",
				payload: {uid: panel.uid},
				mbody: {option: "standard"}
			}, panel, panel.rs_loadItemContent, false);
		req.send();
	},
	
	rs_loadItemContent: function(xdoc) {
		var me = this,
			item = new IG$._I9e/*mr_javaclass*/(xdoc),
			jdom,
			jdomeditor,
			jview,
			w, h;
			
		me.item = item;
		jdom = $(me.down("[name=javasrc]").el.dom);
		// setValue(me.item.javasource || me.item.P2/*getDefaultScript*/());
		jdomeditor = $(".idv-jcl-edtr", jdom);
		jdomeditor.empty();
		
		w = IG$.j$ext._w(jdom);
		h = IG$.j$ext._h(jdom);
		
		jview = $("<div class='idv-jcl-view'></div>").appendTo(jdomeditor);
		IG$.j$ext._w(jview, w);
		IG$.j$ext._h(jview, h);
		
		if (!window.ace)
		{
			IG$.getScriptCache(ig$.bootconfig.scmap.igcg, new IG$.callBackObj(me, function() {
				var editor = IG$.ace_edit(jview[0], "java");
				editor.setValue(me.item.javasource || me.item.P2/*getDefaultScript*/());
				me.scriptview = jview;
				me.scripteditor = editor;
			}));
		}
		else
		{
			var editor = IG$.ace_edit(jview[0], "java");
			editor.setValue(me.item.javasource || me.item.P2/*getDefaultScript*/());
			me.scriptview = jview;
			me.scripteditor = editor;
		}
	},
	
	saveMetaContent: function(smode) {
		var panel = this,
			req = new IG$._rpc$(),
			cnt;
		
		panel.item.javasource = panel.scripteditor.getValue(); // panel.down("[name=javasrc]").getValue();
		
		cnt = panel.item.l2/*toXMLString*/.call(panel.item);
		
		req.init(panel, 
			{
				ack: "writecontent",
				payload: {uid: panel.uid},
				mbody: cnt
			}, panel, panel.rs_saveMetaContent, null, smode);
		req.send();
	},
	
	rs_saveMetaContent: function(xdoc, smode) {
		var me = this;
		
		if (smode == "compile")
		{
			me.doCompile();
		}
		else
		{
			IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
		}
	},
	
	doCompile: function() {
		var panel = this,
			req = new IG$._rpc$(),
			cnt;
		
		cnt = panel.item.l2/*toXMLString*/.call(panel.item);
		
		req.init(panel, 
			{
				ack: "bizop",
				payload: {uid: panel.uid, option: "compile"},
				mbody: cnt
			}, panel, panel.rs_doCompile, null);
		req.send();
	},
	
	rs_doCompile: function(xdoc) {
		var me = this,
			tnode = IG$.XGetNode(xdoc, "/smsg/result/errormessage"),
			errmsg;
			
		if (tnode)
		{
			errmsg = IG$.XGetTextVal(tnode);
			errmsg = Base64.decode(errmsg);
			me.down("[name=compileres]").setValue(errmsg);
		}
		else
		{
			me.down("[name=compileres]").setValue("Compiled Successfully");
		}
	},
	
	toolbarHandler: function(cmd) {
		var me = this;
		
		switch (cmd)
		{
		case "cmd_save":
			me.saveMetaContent();
			break;
		case "cmd_compile":
			me.saveMetaContent("compile");
			break;
		}
	},

	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					name: "javasrc",
					region: "center",
					html: "<div class='idv-jcl-edtr'></div>",
					split: true,
					listeners: {
						resize: function(tobj, w, h, ow, oh, opts) {
							if (me.scriptview && me.scripteditor)
							{
								IG$.j$ext._w(me.scriptview, w);
								IG$.j$ext._h(me.scriptview, h);
								me.scripteditor.resize(true);
							}
						}
					}
				},
				{
					xtype: "panel",
					height: 120,
					region: "south",
					split: true,
					layout: "fit",
					title: "Compile result",
					items: [
						{
							xtype: "textarea",
							name: "compileres",
							height: 120,
							region: "south"
						}
					]
				}
			],
			tbar: [
				{
					iconCls: 'icon-toolbar-save',
					name: "t_save",
					tooltip: IRm$.r1('L_SAVE_CONTENT'),
					handler: function() {
						this.toolbarHandler('cmd_save'); 
					},
					scope: this
				},
				{
					iconCls: 'icon-toolbar-saveas',
					name: "t_save_as",
					tooltip: IRm$.r1('L_SAVE_CONTENT_AS'),
					handler: function() {
						this.toolbarHandler('cmd_compile'); 
					},
					scope: this
				}
			]
		});
			  
		IG$.mr_javaeditor.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			var me = this;
						
			me.init_f();
		}
	}
	
});