IG$.bA_6/*interactive_learning*/ = $s.extend($s.window, {
	modal: true,
	isWindow: true,
	region:"center",
	layout: {
		type: "fit",
		align: "stretch"
	},
	
	closable: false,
	resizable:false,
	width: 400,
	autoHeight: true,
	
	callback: null,
	
	init_f: function() {
		var me = this,
			itemobj = me.itemobj,
			g_editor = me.down("[name=g_editor]");
		
		g_editor.store.loadData(itemobj.interactive_learning.showable_edit_proposals || []);
	},
	
	_confirm: function() {
		var me = this;
			
		me.callback && me.callback.execute({
		});
		
		me.close();
	},
			
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1("T_INT_LEARN");
				 
		$s.apply(this, {
			
			items: [
				{
					xtype: "panel",
					layout: "fit",
					border: 0,
					items: [
						{
							xtype: "panel",
							bodyPadding: 10,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "gridpanel",
									name: "g_editor",
									height: 300,
									store: {
										fields: ["description", "motivation"]
									},
									columns: [
										{
											text: "Edit description",
											dataIndex: "description",
											flex: 1
										},
										{
											text: "Motivation",
											dataIndex: "motivation",
											width: 100
										}
									]
								}
							]
						}
					]
				}
			],
			buttons:[
				"->",
				{
					text: IRm$.r1("B_CONFIRM"),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, {
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui) {
					var panel = this;
					panel.init_f();
				}
			}
		});
		
		IG$.bA_6/*interactive_learning*/.superclass.initComponent.apply(this, arguments);
	}
});