IG$._Ia0/*hadoop_logviewer*/ = $s.extend($s.window, {
	
	modal: false,
	isWindow: true,
	region:"center",
	layout: "fit",
	
	closable: false,
	resizable:false,
	width: 500,
	autoHeight: true,
		
	callback: null,
	
	init_f: function() {
		var me = this;
		
		if (me.uid)
		{
			me.rL/*refreshLog*/();
		}
	},
	
	
	_confirm: function() {
		var me = this;
		
		me.closeDlg();
	},
	
	closeDlg: function() {
		var me = this;
		me.callback && me.callback.execute(me.job);
		me.fireEvent("close_dlg", me);
	},
	
	rL/*refreshLog*/: function() {
		var me = this;
		
		if (me.uid)
		{
			var me = this,
				lreq = new IG$._rpc$();
			lreq.init(me, 
				{
					ack: "hadoophelper",
					payload: {uid: me.uid},
					mbody: {option: "loghistory"}
				}, me, me.rs_rL/*refreshLog*/, false);
				
			lreq.send();
		}
	},
	
	rs_rL/*refreshLog*/: function(xdoc) {
		var me = this,
			tnode = IG$.XGetNode(xdoc, "/smsg"),
			tnodes = (tnode ? IG$.XGetChildNodes(tnode) : null),
			i, logs = [], l,
			grdloghist = me.down("[name=grdloghist]"),
			pdesc,
			duration, m, s, ms;
			
		if (tnodes)
		{
			for (i=0; i < tnodes.length; i++)
			{
				l = IG$.XGetAttrProp(tnodes[i]);
				l.pstatus = parseInt(l.pstatus);
				l.created_desc = IG$.formatDate(l.created);
				l.updated_desc = IG$.formatDate(l.updated);
				duration = Number(l.duration);
				duration = isNaN(duration) ? null : duration;
				l.duration = me.gg2/*getDuration*/(duration);
				
				pdesc = me.gg1/*getStatusDesc*/(l.pstatus);
				
				l.pstatus_desc = pdesc;
				logs.push(l);
			}
		}
		
		grdloghist.store.loadData(logs);
	},
	
	gg1/*getStatusDesc*/: function(s) {
		var pdesc = "Unknown";
		switch (s)
		{
		case 1:
			pdesc = "Running";
			break;
		case 10:
			pdesc = "Completed";
			break;
		case 20:
			pdesc = "Error: No result";
			break;
		case 21:
			pdesc = "Error: Dataformat mismatch";
			break;
		case 29:
			pdesc = "Error: Execution failed";
			break;
		}
		
		return pdesc;
	},
	
	gg2/*getDuration*/: function(duration) {
		var r = null,
			m, s, ms;
		
		if (duration != null)
		{
			m = Math.floor(duration / (60*1000));
			duration -= m * (60*1000);
			s = Math.floor(duration / (1000));
			duration -= s * (1000);
			ms = duration;
			r = (m>0 ? m + "m " : "") + (s>0 ? s + "s " : "") + (ms>-1 ? ms + "ms" : "");
		}
		
		return r;
	},
	
	g1/*getLogDetail*/: function(sid) {
		var me = this,
			lreq = new IG$._rpc$();
		lreq.init(me, 
			{
				ack: "hadoophelper",
				payload: {sid: sid},
				mbody: {option: "logjoblist"}
			}, me, me.rs_g1/*getLogDetail*/, false);
			
		lreq.send();
	},
	
	rs_g1/*getLogDetail*/: function(xdoc) {
		var me = this,
			tnode = IG$.XGetNode(xdoc, "/smsg"),
			tnodes = (tnode ? IG$.XGetChildNodes(tnode) : null),
			i, dp = [],
			grdjobhist = me.down("[name=grdjobhist]"),
			l, pdesc, duration;
			
		if (tnodes)
		{
			for (i=0; i < tnodes.length; i++)
			{
				l = IG$.XGetAttrProp(tnodes[i]);
				l.pstatus = parseInt(l.pstatus);
				l.created_desc = IG$.formatDate(l.created);
				l.updated_desc = IG$.formatDate(l.updated);
				pdesc = me.gg1/*getStatusDesc*/(l.pstatus);
				duration = Number(l.duration);
				duration = isNaN(duration) ? null : duration;
				l.duration = me.gg2/*getDuration*/(duration);
				
				l.pstatus_desc = pdesc;
				
				dp.push(l);
			}
		}
		
		grdjobhist.store.loadData(dp);
	},
		
	initComponent : function() {
		var panel = this,
			i;
			
		panel.title = IRm$.r1("L_LOG_VIEW");
		
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "fieldset",
							title: "Execution History",
							layout: "fit",
							items: [
								{
									xtype: "gridpanel",
									name: "grdloghist",
									store: {
										type: "store",
										fields: [
											"created", "updated", "sid", "duration", "pstatus", "created_desc", "updated_desc", "pstatus_desc"
										]
									},
									height: 120,
									columns: [
										{
											xtype: "gridcolumn",
											text: "Started time",
											dataIndex: "created_desc",
											flex: 1
										},
										{
											xtype: "gridcolumn",
											text: "End time",
											dataIndex: "updated_desc",
											flex: 1
										},
										{
											xtype: "gridcolumn",
											text: "Status",
											dataIndex: "pstatus_desc",
											width: 120
										}
									],
									listeners: {
										itemclick: function(view, record, item, index, e) {
											var sid = record.get("sid");
											
											if (sid)
											{
												this.g1/*getLogDetail*/(sid);
											}
										},
										scope: this
									}
								}
							]
						},
						{
							xtype: "fieldset",
							title: "Log detail",
							layout: "fit",
							items: [
								{
									xtype: "gridpanel",
									name: "grdjobhist",
									height: 240,
									store: {
										xtype: "store",
										fields: [
											"sid", "duration", "jobtype", "pstatus", "created", "updated",
											"pstatus_desc",
											"created_desc", "updated_desc"
										]
									},
									columns: [
										{
											xtype: "gridcolumn",
											text: "Job type",
											dataIndex: "jobtype",
											flex: 1
										},
										{
											xtype: "gridcolumn",
											text: "Duration",
											width: 50,
											dataIndex: "duration"
										},
										{
											xtype: "gridcolumn",
											text: "Started time",
											dataIndex: "created_desc",
											width: 80
										},
										{
											xtype: "gridcolumn",
											text: "End time",
											dataIndex: "updated_desc",
											width: 80
										},
										{
											xtype: "gridcolumn",
											text: "Status",
											dataIndex: "pstatus_desc",
											width: 80
										}
									]
								}
							]
						}
					]
				}
			],
			buttons:[
//				{
//					text: IRm$.r1("B_HELP"),
//					handler: function() {
//						IG$.showHelp("P0022");
//					},
//					scope: this
//				}, 
				"->",
				{
					text: IRm$.r1("B_CLOSE"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui) {
					var panel = this;
					panel.init_f();
				}
			}
		});
		
		IG$._Ia0/*hadoop_logviewer*/.superclass.initComponent.apply(this, arguments);
	}
});